import { ComponentFactoryResolver, Directive, Input, OnInit, ViewContainerRef, Output, EventEmitter } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { InputComponent } from "../input/input.component";
import { SelectComponent } from "../select/select.component";
import { RadiobuttonComponent } from "../radiobutton/radiobutton.component";
import { CheckboxComponent } from "../checkbox/checkbox.component";
import { ButtonComponent } from '../button/button.component';
import { SubtitleComponent } from '../subtitle/subtitle.component';
import { Field } from 'src/app/models/template.model';
import { UploadComponent } from '../upload/upload.component';
import { IntegratedSearchComponent } from '../integrated-search/integrated-search.component';
import { TextComponent } from '../text/text.component';
import { TableComponent } from '../table/table.component';
import { ComparationComponent } from '../comparation/comparation.component';

const componentMapper = {
	button: ButtonComponent,
	comparation: ComparationComponent,
	input: InputComponent,
	integratedSearch: IntegratedSearchComponent,
	select: SelectComponent,
	radiobutton: RadiobuttonComponent,
	checkbox: CheckboxComponent,
	subtitle: SubtitleComponent,
	upload: UploadComponent,
	table: TableComponent,
	text: TextComponent
};

@Directive({
	selector: '[appDynamicField]'
})
export class DynamicFieldDirective implements OnInit {

	@Input() field: Field;
	@Input() group: FormGroup;
	@Input() dataTable: any[];
	@Output() changeValue: EventEmitter<any> = new EventEmitter<any>();

	private componentRef: any;
	
	constructor (
		private resolver: ComponentFactoryResolver,
		private container: ViewContainerRef
	) {  }

	ngOnInit() {
		const factory = this.resolver.resolveComponentFactory(
			componentMapper[this.field.type]
		);
		this.componentRef = this.container.createComponent(factory);
		this.componentRef.instance.field = this.field;
		this.componentRef.instance.group = this.group;
		this.componentRef.instance.dataTable = this.dataTable;

		this.group.valueChanges.subscribe(
			response => {
				this.changeValue.emit(response);
			}
		);
	}


}
