export class PostAnalyse {
    data: Object;
    statusId: number;
    documento: string;
    agencia: string;
    contaCorrente: string;
    numeroProcesso: string;
    numeroPasta: string;
    atividadeAcao: number[];
    motivosRejeicao: number[];
    motivosContingencia: number[];
    templateVersaoId: number;
    anexo: any[];
}

export class Analyse extends PostAnalyse {
    receberProximo: boolean;
    entrarPausa: boolean;
    tipoPausaId: number;
    finalizarExpediente: boolean;
}

export class OpenTicket extends PostAnalyse {
    timestampInicial: string;
    atividadeId: number;
}


