import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ResponseList } from 'src/app/models/response-list.interface';

@Injectable({ providedIn: 'root' })
export class HistoricoService {
  
    constructor (
        private http: HttpClient,
    ) {}

	public get(documento:string, agencia:string, contaCorrente:string): Observable<any> {
		return this.http.get<ResponseList>(`${environment.urlServices}/api/v1/analises/historico-macro`, {
			params : {
                Agencia : agencia, 
                ContaCorrente : contaCorrente,
				Documento : documento
			}
		});
    }     


	public getKit(kitId:string): Observable<any> {
		return this.http.get<ResponseList>(`${environment.urlServices}/api/v1/kit/${kitId}/historico`);
    }     

	public getHistoricoKit(esteiraId:string): Observable<any> {
		return this.http.get<ResponseList>(`${environment.urlServices}/api/v1/analises/etapas/` + esteiraId);
    }     

}