import { Component, EventEmitter, Input, OnInit, Output, AfterViewInit, OnDestroy } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MessengerService } from 'src/app/services/messenger.service';
import { Step, Condition, Field, Decision, RejectionReasons, ContingencyReasons, Action } from 'src/app/models/template.model';
import { MatDialog, MatSelectChange, MatCheckboxChange } from '@angular/material';
import { Subscription, Subject } from 'rxjs';
import { Dialog } from 'src/app/models/dialog.model';
import { DialogComponent } from '../dialog/dialog.component';
import { PostAnalyse } from 'src/app/models/post-analyse.class';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { takeUntil } from 'rxjs/operators';
import { TemplateTypes } from 'src/app/models/template-types.enum';
import { StatusAnalyse } from 'src/app/models/status-analyse.interface';
import { element } from 'protractor';

@Component({
	exportAs: "dynamicForm",
	selector: 'app-dynamic-form',
	templateUrl: './dynamic-form.component.html',
	styleUrls: ['./dynamic-form.component.scss']
})
export class DynamicFormComponent implements OnInit, OnDestroy, AfterViewInit {

	@Input() content: Object;
	@Input() steps: Step[] = [];
	@Input() decision: Decision;
	@Input() rejectionReasons: RejectionReasons[] = [];
	@Input() contingencyReasons: ContingencyReasons[];
	@Input() actions: Action[];
	@Input() activities: number[] = [];
	@Input() type: TemplateTypes;
	@Input() status: StatusAnalyse;
	@Input() anexos: any[] = [];
	@Output() submitDynamicForms: EventEmitter<any> = new EventEmitter<any>();

	public form: FormGroup;
	public subscription: Subscription;
	private conditionsToVisible: boolean[] = [];
	private conditionsToEnable: boolean[] = [];
	private fieldsForm: Field[] = [];
	private ngUnsubscribe = new Subject();

	private decisionId: string;
	private templateV1: boolean;

	public rejectionReasonsChoosed: number[] = [];
	public showRejectionReasons: boolean = false;

	public contingencyReasonsChoosed: number[] = [];
	public showContingencyReasons: boolean = false;

	public buttonInFocus: string = "";

	private tableIsValid: boolean = false;

	public dataFilterMotivo: RejectionReasons[] = [];

	constructor (
		private fb: FormBuilder,
		private messengerService: MessengerService,
		public dialog: MatDialog,
	) { }

	ngOnInit() {
		this.form = this.createControl();

		/** Teste para verificar a versão do template */
		if (this.decision && this.decision.buttons.length > 0) {
			this.templateV1 = false;
		} else {
			this.templateV1 = true;
		}

		if (this.type === TemplateTypes.EDIT) {
			this.activities = this.actions.filter(el => el.checked).map(el => el.value);
		}

		if (this.type === TemplateTypes.VIEW) {
			this.steps.forEach(step => {
				step.fieldGroups.forEach(fieldGroup => {
					fieldGroup.fields.forEach(field => {
						field.disabled = true;
						field.visible = true;
					})
				});
			})

			if (this.decision && this.decision.buttons.length > 0) {
				this.decision.buttons.forEach(button => {
					button.disabled = true;
				});
			}

			if (this.status.id === 4) {
				this.showRejectionReasons = true;
			}
		}

	}

	ngAfterViewInit() {
		this.form.valueChanges.pipe(takeUntil(this.ngUnsubscribe)).subscribe(
			response => {
				this.fieldsForm.forEach(field => {
					this.testAllConditions(field, response);
				})

				if (this.decision) {
					this.decision.buttons.forEach(button => {
						this.testAllConditions(button, response);
					})
				}
			}
		)

		this.subscription = this.messengerService.getMessage().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
			message => {
				let fieldSource: Field = new Field();

				this.fieldsForm.forEach(field => {
					if (field.name === message.source) {
						fieldSource = field;
					}
				});

				/** Pesquisa Integrada */
				if (fieldSource.type === 'integratedSearch') {
					fieldSource.integratedSearch.returnIntegratedSearch.forEach(el => {
						this.fieldsForm.forEach(field => {
							if (field.name === el.nameField) {
								if (message.data !== undefined) {
									Object.entries(message.data).forEach(data => {
										if (data[0].toLowerCase() === el.nameReturn.toLowerCase()) {
											this.form.get(field.name).setValue(data[1]);
										}
									})
								} else {
									this.form.get(field.name).setValue('');
								}
							}
						})
					})
				}

				/** Upload de Arquivos */
				if (message.source === 'upload') {
					this.anexos = message.data;
				}

				/** Validação de Tabelas */
				if (fieldSource.type === 'table' && message.data === 'tableValid') {
					this.tableIsValid = true;
					this.onSubmit();
				}

				/** Template 1.0 */
				if (this.templateV1 && message.data && message.data.buttonType === 'submit') {
					this.decisionId = message.data.actionButton.decision;
					this.onSubmit();
				}

				/** Template 2.0 */
				if (!this.templateV1 && message.data && message.data.buttonType === 'submit') {
					this.decision.buttons.forEach(button => {
						if (button.name === message.source) {
							if (this.buttonInFocus === message.source) {
								this.buttonInFocus = "";
							} else {
								this.buttonInFocus = button.name;
							}
						}
					});

					this.decisionId = message.data.actionButton.decision;

					if (message.data.actionButton.decision === '4' && !this.showRejectionReasons) {
						this.showRejectionReasons = true;
					} else {
						this.showRejectionReasons = false;
						this.rejectionReasonsChoosed = [];
					}

					if (message.data.actionButton.decision === '8' && !this.showContingencyReasons) {
						this.showContingencyReasons = true;
					} else {
						this.showContingencyReasons = false;
						this.contingencyReasonsChoosed = [];
					}

					if (message.data.actionButton.decision !== '4' && message.data.actionButton.decision !== '8') {
						this.onSubmit();
						return;
					}

					this.decision.buttons.forEach(button => {
						if (this.buttonInFocus !== "" && button.name !== this.buttonInFocus) {
							button.disabled = true;
						} else {
							if (button.conditionsToEnable.length > 0) {
								this.testAllConditions(button, this.form.value);
							} else {
								button.disabled = false;
							}
						}
					})
				}
			}
		);

		setTimeout(() => {
			if (this.type !== TemplateTypes.VIEW) {
				this.form.updateValueAndValidity();
			}
		}, 0);
	}

	ngOnDestroy() {
		this.ngUnsubscribe.next();
		this.ngUnsubscribe.complete();
	}

	private testAllConditions(field: Field, response: any): void {

		/** Testa condições de visibilidade */
		if (field.conditionsToVisible.length > 0) {

			this.conditionsToVisible = [];
			field.conditionsToVisible.forEach(condition => {
				this.testFieldsToVisible(response, condition);
			});

			if (this.conditionsToVisible.indexOf(false) < 0) {
				field.visible = true;
			} else {
				field.visible = false;
			}
		}

		/** Testa condições para os campos estarem habilitados */
		if (field.conditionsToEnable.length > 0) {

			this.conditionsToEnable = [];

			field.conditionsToEnable.forEach(condition => {
				this.testFieldsToEnable(response, condition)
			});

			this.enableField(field);
		}

		/** Definir valor de campo se opção for selecionada */
		if ((field.type === 'select' || field.type === 'radiobutton') && response[field.name] !== "") {
			field.options.map(option => {
				if (response[field.name] === option.value && option.conditionOption) {
					option.conditionOption.map(conditionOption => {
						const field: Field = this.fieldsForm.find(field => field.name === conditionOption.fieldName);
						if (this.form.get(conditionOption.fieldName).value !== conditionOption.value) {
							if (conditionOption.value === 'DATASOURCE') {
								if (field.dataSource) {
									const valueDataSource: string[] = Object.entries(this.content).find(el => el[0].toUpperCase() === field.dataSource.toUpperCase());
									if (this.form.get(conditionOption.fieldName).value !== valueDataSource[1]) {
										this.form.get(conditionOption.fieldName).setValue(valueDataSource[1]);
									}
								}
							} else {
								this.form.get(conditionOption.fieldName).setValue(conditionOption.value);
							}
						};
					});
				};
			});
		};
	}

	private testFieldsToVisible(response: any, condition: Condition): void {

		if (response[condition.fieldName] == undefined) {
			return;
		}

		if (condition.value.toString() === response[condition.fieldName].toString()) {
			this.conditionsToVisible = [...this.conditionsToVisible, true]
		} else {
			this.conditionsToVisible = [...this.conditionsToVisible, false]
		}
	}

	private testFieldsToEnable(response: any, condition: Condition): void {

		if (response[condition.fieldName] == undefined) {
			return;
		}

		switch (condition.operator) {
			case 'equal':
				if (condition.value.toString() === response[condition.fieldName].toString()) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;

			case 'notEqual':
				if (condition.value.toString() !== response[condition.fieldName].toString()) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;

			case 'greaterThan':
				if (Number(condition.value) < Number(response[condition.fieldName])) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;

			case 'lessThan':
				if (Number(condition.value) > Number(response[condition.fieldName])) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;

			default:
				const sameCondition = condition.value.toString() === response[condition.fieldName].toString();
				this.conditionsToEnable = [...this.conditionsToEnable, sameCondition];
				break;
		}
	}

	private enableField(field: Field): void {

		let result: boolean;

		switch (field.conditionsToEnableSatisfy) {
			case 'all':
				if (this.conditionsToEnable.indexOf(false) < 0) {
					result = true;
				} else {
					result = false;
				}
				break;

			case 'onlyOne':
				if (this.conditionsToEnable.indexOf(true) >= 0) {
					result = true;
				} else {
					result = false;
				}
				break;

			default:
				result = (this.conditionsToEnable.indexOf(false) < 0);
				break;
		}

		if (field.type === "button" || field.type === "upload") {
			if (result) {
				field.disabled = false;
			} else {
				field.disabled = true;
			}
		} else if (field.type === "input" && field.readOnly !== undefined && field.readOnly !== "") {
			if (result) {
				field.readOnly = false;
			} else {
				field.readOnly = true;
			}
		} else {
			if (result) {
				if (this.form.get(field.name).disabled) {
					this.form.get(field.name).enable();
				}
			} else {
				if (this.form.get(field.name).enabled) {
					this.form.get(field.name).disable();
				}
			}
		}
	}

	get value() {
		return this.form.value;
	}

	private createControl() {
		const group = this.fb.group({});
		this.steps.forEach(step => {
			step.fieldGroups.forEach(fieldGroup => {
				fieldGroup.fields.forEach(field => {
					if (field.type == 'upload') {
						if (this.anexos != undefined && this.anexos.length > 0) {
							field.value = this.anexos;
						}
					}
					this.fieldsForm = [...this.fieldsForm, field];
					if (field.type === "button" && field.buttonType === "submit") return;
					if (field.type === "subtitle") return;
					const control = this.fb.control(field.value, (field.required === true ? [Validators.required] : []));
					group.addControl(field.name, control);
				})
			})
		});
		return group;
	}

	public getReasonsToReject(value: any): RejectionReasons[] {
		if (this.type === TemplateTypes.VIEW && this.status.id === 4) {
			this.dataFilterMotivo = this.rejectionReasons.filter(el => el.checked);
		} else {
			this.dataFilterMotivo = this.rejectionReasons.filter(el => el.value !== 9999);
		}
		
		if(value !== null || value !== undefined || value !== '') {
			this.dataFilterMotivo = this.dataFilterMotivo.filter(item => item.name.toLocaleLowerCase().includes(value.toLocaleLowerCase()));
		} 
		
		return this.dataFilterMotivo;	
	}

	public getReasonsToRejectChoosed(): number[] {
		return this.rejectionReasonsChoosed;
	}

	public setReasonsToReject(event: MatSelectChange): void {
		this.rejectionReasonsChoosed = [];
		this.rejectionReasonsChoosed = [...event.value];
	}

	public setReasonsToContingency(event: MatSelectChange): void {
		this.contingencyReasonsChoosed = [];
		this.contingencyReasonsChoosed = [event.value];
	}

	public setActivity(event: MatCheckboxChange, value: number): void {
		if (event.checked) {
			this.activities = [...this.activities, value];
		} else {
			this.activities = this.activities.filter(el => el !== value);
		}
	}

	public onSubmit(): void {
		if (this.validationsToSubmit()) {

			/** Retirar Botões Condicionais */
			const buttons: Field[] = this.fieldsForm.filter(el => el.buttonType === "conditional");
			const dataForm: any = Object.assign({}, this.form.value);

			buttons.forEach(el => {
				delete dataForm[el.name];
			});

			const body: PostAnalyse = new PostAnalyse();
			body.data = dataForm;
			body.statusId = Number(this.decisionId);
			body.motivosRejeicao = [...this.rejectionReasonsChoosed];
			body.motivosContingencia = [...this.contingencyReasonsChoosed];
			body.atividadeAcao = [...this.activities];
			body.anexo = this.anexos;
			this.submitDynamicForms.emit(body);
			this.tableIsValid = false;
		}
	}

	private validationsToSubmit(): boolean {

		let validations: boolean[] = [];
		let requiredFieldsInvalid: string[] = [];
		let tableName: string;

		/** 
		    Decisões possíveis:
		 	"Aprovar" = "5",
			"Concluir" = "6",
			"Contingênciar" = "8",
			"Postergar" = "3",
			"Reprovar" = "4",
		*/

		/** Verificação dos campos obrigatórios */
		this.fieldsForm.map(field => {

			if (field.type === 'table') {
				tableName = field.name;
			}

			if (field.required) {
				if (this.form.get(field.name).value === "") {
					validations = [...validations, false];
					requiredFieldsInvalid = [...requiredFieldsInvalid, field.label];
				} else {
					validations = [...validations, true];
				}
			}
		});

		if (validations.indexOf(false) >= 0 && this.decisionId !== '8') {
			const dialog: Dialog = new Dialog();
			dialog.title = "Campos Obrigatórios não preenchidos";
			dialog.message = "Você precisa preencher todos os campos obrigatórios antes de prosseguir:";
			dialog.message += "<br /><br /><ul>";
			requiredFieldsInvalid.forEach(element => {
				dialog.message += `<li>${element}</li>`;
			});
			dialog.message += "</ul>";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
			this.form.markAllAsTouched();
			return false;
		}

		if (this.activities.length < 1 && this.decisionId !== '8' && !this.templateV1) {
			const dialog: Dialog = new Dialog();
			dialog.title = "Ações Realizadas na Atividade";
			dialog.message = "Você precisa realizar ao menos uma ação em cada análise.";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
			return false;
		}

		if (this.decisionId === '4' && this.rejectionReasons.length < 1 && !this.templateV1) {
			const dialog: Dialog = new Dialog();
			dialog.title = "Motivos da Reprovação";
			dialog.message = "Você precisa escolher um motivo para a reprovação";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
			return false;
		}

		/** Validação de Tabelas */
		if (tableName && !this.tableIsValid) {
			this.messengerService.dispatchAction(tableName, 'validTableToSubmit');
			return false;
		}

		return true;
	}

	public showButtonSubmit(): boolean {
		if (this.decisionId === '4' && this.rejectionReasonsChoosed.length > 0) {
			return true;
		} else if (this.decisionId === '8' && this.contingencyReasonsChoosed.length > 0) {
			return true;
		} else {
			return false;
		}
	}

	public showMessage(dialog: Dialog): void {
		const dialogRef = this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title: dialog.title,
				message: dialog.message,
				type: dialog.type,
			}
		});
		dialogRef.afterClosed().pipe(takeUntil(this.ngUnsubscribe)).subscribe();
	}

}
