import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Field } from 'src/app/models/template.model';

@Component({
	selector: 'app-radiobutton',
	templateUrl: './radiobutton.component.html',
	styleUrls: ['./radiobutton.component.scss']
})
export class RadiobuttonComponent implements OnInit {

	public field: Field;
	public group: FormGroup;
	
	constructor() { }
	
	ngOnInit() {
		this.field.visible = Boolean(this.field.visible.toString() === "true" ? true : false);
		this.field.disabled = Boolean(this.field.disabled.toString() === "true" ? true : false);
		this.field.required = Boolean(this.field.required.toString() === "true" ? true : false);

		if (this.field.required && !this.field.label.includes("(obrigatório)") && this.field.label !== '') {
			this.field.label = this.field.label +" (obrigatório)";
		}
		
		if (this.field.disabled) {
			this.group.get(this.field.name).disable();
		}
	}
}
