import { AbstractControl } from '@angular/forms';
import { BrValidator } from 'src/app/shared/validators/br-validators';
import * as moment from 'moment';

export class FormValidator {
	public static documento(abstractControl: AbstractControl): any {
		const documento = abstractControl.value;
		if (BrValidator.cpf(documento) || BrValidator.cnpj(documento)) {
			return null;
		}
		return { isError: true };
	}

	public static cpf(abstractControl: AbstractControl): any {
		const documento = abstractControl.value;
		if (BrValidator.cpf(documento)) {
			return null;
		}
		return { isError: true };
	}

	public static cnpj(abstractControl: AbstractControl): any {
		const documento = abstractControl.value;
		if (BrValidator.cnpj(documento)) {
			return null;
		}
		return { isError: true };
	}

	public static horaMinuto(abstractControl: AbstractControl): any {
		const date = abstractControl.value;
		//valida tamanho
		if (date.length != 5) {
			return { isError: true };
		}

		//valida minutos
		const hora = parseInt(date.substring(0, 2));
		if (date.substring(0, 2).length != 2 || hora < 0 || hora > 23) {
			return { isError: true };
		}

		//valida minutos
		const minutos = parseInt(date.substring(3, 5));
		if (date.substring(3, 5).length != 2 || minutos < 0 || minutos > 59) {
			return { isError: true };
		}

		return null;
	}

	public static horaMinutoTimeSpan(abstractControl: AbstractControl): any {
		const date = abstractControl.value;
		//valida tamanho
		if (date.length != 5) {
			return { isError: true };
		}

		//valida minutos
		const hora = parseInt(date.substring(0, 2));
		if (date.substring(0, 2).length != 2 || hora < 0 || hora > 99) {
			return { isError: true };
		}

		//valida minutos
		const minutos = parseInt(date.substring(3, 5));
		if (date.substring(3, 5).length != 2 || minutos < 0 || minutos > 59) {
			return { isError: true };
		}

		return null;
	}

	public static dataHoraMinuto(abstractControl: AbstractControl): any {
		const date = abstractControl.value;
		//valida tamanho
		if (date.length != 16) {
			return { isError: true };
		}

		//valida dia
		const dia = parseInt(date.substring(0, 2));
		if (date.substring(0, 2).length != 2 || dia < 0 || dia > 31) {
			return { isError: true };
		}

		//valida mes
		const mes = parseInt(date.substring(3, 5));
		if (date.substring(3, 5).length != 2 || mes < 1 || mes > 12) {
			return { isError: true };
		}

		//valida ano
		const ano = parseInt(date.substring(6, 10));
		if (date.substring(6, 10).length != 4 || ano < 1900) {
			return { isError: true };
		}

		//valida hora
		const hora = parseInt(date.substring(11, 13));
		if (date.substring(11, 13).length != 2 || hora < 0 || hora >= 25) {
			return { isError: true };
		}

		//valida minutos
		const minutos = parseInt(date.substring(14, 16));
		if (date.substring(14, 16).length != 2 || minutos < 0 || minutos >= 60) {
			return { isError: true };
		}

		if (moment(date, 'DD-MM-YYY HH:mm').isValid()) {
			return null;
		}
		return { isError: true };
	}

	public static agencia(abstractControl: AbstractControl): any {
		const agencia = abstractControl.value;
		if (BrValidator.agencia(agencia)) {
			return null;
		}
		return { isError: true };
	}

	public static contaDigitos(abstractControl: AbstractControl): any {
		const conta = abstractControl.value;
		if (BrValidator.contaDigitos(conta)) {
			return null;
		}
		return { isError: true };
	}

	public static etiqueta(abstractControl: AbstractControl): any {
		const valor = abstractControl.value;
		if (BrValidator.etiqueta(valor)) {
			return null;
		}
		return { isError: true };
	}
}