import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Option, Condition, IntegratedSearch, Field, ReturnIntegratedSearch, ActionButton, ConditionOption } from 'src/app/models/template.model';
import { MatDialogRef, MatTableDataSource, MatCheckboxChange, MatSelectChange, MatSelect, MatDialog, MatRadioChange } from '@angular/material';
import { DialogComponent } from '../dialog/dialog.component';
import { DataSourceIntegratedSearch, FieldIntegratedSearch } from 'src/app/models/data-source-integrated-search.interface';
import { FieldType } from 'src/app/models/field.type.enum';
import { InputType } from 'src/app/models/input-type.enum';
import { CssGrid } from 'src/app/models/css-grid.enum';
import { ButtonType, ButtonSubmitDecisions } from 'src/app/models/button-type.enum';
import { Masks } from 'src/app/models/masks.enum';
import { Operators } from 'src/app/models/operator.enum';
import { PredefinedFields } from 'src/app/models/predefined-fields.enum';
import { TypeIntegratedSearch } from 'src/app/models/type-integrated-search.enum';
import { GeneralInformationsService } from 'src/app/services/general-informations.service';
import { Dialog } from 'src/app/models/dialog.model';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { ConfigurationServiceUtilService } from 'src/app/modules/configuration-system/services/configuration-system-util.service';
import { DialogAddColumnComponent } from './dialog-add-column/dialog-add-column.component';
import { ColumnsTable } from 'src/app/models/template.model';
import { moveItemInArray, CdkDragDrop } from '@angular/cdk/drag-drop';


@Component({
	selector: 'app-add-field-form',
	templateUrl: './add-field-form.component.html',
	styleUrls: ['./add-field-form.component.scss']
})
export class AddFieldFormComponent implements OnInit {

	@Input() formFields: Field[] = [];
	@Input() editingField: boolean;
	@Input() isTable: boolean;
	@Input() isValid: boolean;
	@Output() saveField: EventEmitter<any> = new EventEmitter<any>();
	@Output() cancelEditionField: EventEmitter<any> = new EventEmitter<any>();

	/** Formulários */
	public form: FormGroup;
	public formConditionsToVisible: FormGroup;
	public formConditionsToEnable: FormGroup;
	public formButtons: FormGroup;
	public formIntegratedSearch: FormGroup;
	public formOptions: FormGroup;
	public formOptionsCondition: FormGroup;
	public formComparation: FormGroup;
	
	/** Opções que vem dos Enuns */
	public fieldTypes: string[][];
	public inputTypes: string[][];
	public buttonTypes: string[][];
	public buttonSubmitDecisions: string[][];
	public cssGrid: string[][];
	public masks: string[][];
	public operators: string[][] = [];
	public predefinedFields: string[][] = [];
	public typeSearchIntegrated: string[][] = [];

	/** Arrays locais para as propriedados do Campo que recebem arrays */
	public fieldOptions: Option[] = [];
	public fieldOptionsCondition: ConditionOption[] = [];
	
	/** Elementos que ajudam a montar os arrays locais */
	public dialogRef: MatDialogRef<DialogComponent, any>;

	/** Campos Escondidos por default */
	public showConditionToVisibleInput: boolean = true;
	public showConditionToVisibleSelect: boolean = false;
	public showConditionToVisibleCheckbox: boolean = false;
	public conditionsToVisibleOptions: Option[] = [];
	public fieldConditionsToVisible: Condition[] = [];
	public dataSource_visible = new MatTableDataSource<Condition>();
	public displayedColumnsConditions: string[] = ['name', 'type', 'operator', 'value', 'actions'];

	/** Campos Desabilitados por default */
	public showConditionToEnableInput: boolean = true;
	public showConditionToEnableSelect: boolean = false;
	public showConditionToEnableCheckbox: boolean = false;
	public conditionsToEnableOptions: Option[] = [];
	public fieldConditionsToEnable: Condition[] = [];
	public dataSource_enable = new MatTableDataSource<Condition>();

	/** Pesquisa Integrada */
	public displayedColumnsIntegratedSearch: string[] = ['nameField', 'nameReturn', 'actions'];
	public dataSource_integratedSearch = new MatTableDataSource<any>();
	public objectIntegratedSearch: IntegratedSearch;
	public originalDataSourceIntegratedSearch: DataSourceIntegratedSearch[] = [];
	public fieldsIntegratedSearch: FieldIntegratedSearch[];

	/** Opções Disponíveis de Selects e RadioButtons */
	public setValueAsDataSource: boolean = false;
	public typeFieldOptionsCondition: string = "";
	public optionsConditionAvailable: Option[] = [];
	public editOptionFlag: boolean = false;
	public indexEditOptionFlag: number;

	/** Tabela */
	public columnsTable: ColumnsTable[] = [];

	/** flags */
	public predefined: boolean;
	public readonly: boolean = false;
	public predefinedType: string;
	public currentDate: boolean;
	public fieldInFocus: string;

	/** opção para campo de texto */
	public modifyDataEsteira: any[] = [{text: 'Documento', value: 'documento'},{text: 'Agência', value: 'agencia'},{text: 'Conta Corrente', value: 'contaCorrente'}]
	
	constructor(
		private generalInformationsService: GeneralInformationsService,
		private formBuilder: FormBuilder,
		public utilService: ConfigurationServiceUtilService,
		public dialog: MatDialog,
	) { }

	ngOnInit() {
		this.fieldTypes = this.isTable ? Object.entries(FieldType).filter(el => el[0] !== 'table') : Object.entries(FieldType);
		this.inputTypes = Object.entries(InputType);
		this.cssGrid = Object.entries(CssGrid);
		this.buttonTypes = Object.entries(ButtonType).filter(el => el[0] !== 'submit');
		this.buttonSubmitDecisions = Object.entries(ButtonSubmitDecisions);
		this.masks = Object.entries(Masks);
		this.operators = Object.entries(Operators);
		this.predefinedFields = Object.entries(PredefinedFields);
		this.typeSearchIntegrated = Object.entries(TypeIntegratedSearch);

		this.generalInformationsService.getDataSourcesIntegratedSearch().subscribe(
			response => {
				this.originalDataSourceIntegratedSearch = response;
			}
		);

		this.form = this.formBuilder.group({
			type : ['', [Validators.required]],
			name : ['', [Validators.required]],
			visible : ['true', [Validators.required]],
			dataSource : [''],
			required : ['false'],
			disabled : ['false'],
			label: [''],
			value : [''],
			cssGrid : ['col-12'],
			cssClass : [''],
			options : [[]],
			conditionsToVisible : [[]],
			conditionsToVisibleSatisfy : [''],
			conditionsToEnable : [[]],
			conditionsToEnableSatisfy : [''],
			inputType : [''],
			selectMultiple : [''],
			mask : [''],
			currentDate : [''],
			maxLength: [''],
			buttonType : [''],
			actionButton : [''],
			modifyDataEsteira: ['']
		});

		this.formConditionsToVisible = this.formBuilder.group({
			field : ['', Validators.required],
			operator : ['', Validators.required],
			value : [''],
		});
		
		this.formConditionsToEnable = this.formBuilder.group({
			field : ['', Validators.required],
			operator : ['', Validators.required],
			value : [''],
		});

		this.formButtons = this.formBuilder.group({
			buttonType : [''],
		});

		this.formIntegratedSearch = this.formBuilder.group({
			searchType : [''],
			criteria : [''],
			mask : [''],
			nameField : ['', Validators.required],
			nameReturn : ['', Validators.required]
		});

		this.formOptions = this.formBuilder.group({
			label : ['', Validators.required],
			value : ['', Validators.required],
		});

		this.formOptionsCondition = this.formBuilder.group({
			fieldName : ['', Validators.required],
			value : ['']
		});

		this.formComparation = this.formBuilder.group({
			source : ['', Validators.required],
			operator : ['', Validators.required],
			compareWith : ['', Validators.required],
			target : ['', Validators.required]
		})
	}

	/********** MÉTODOS PARA CONTROLAR EXIBIÇÃO DE CAMPOS NO FORMULÁRIO  **********/
	public adjustFields(type: string): void {

		this.resetForm();
		this.fieldInFocus = type;
		
		switch (type) {
			case "button":
				this.form.patchValue({
					type : 'button',
					required : 'false',
				});
				this.form.get('dataSource').disable();
				this.form.get('value').disable();
				this.form.get('required').disable();
				break;

			case "comparation":
				this.form.patchValue({
					type : 'comparation',
				})
				this.form.get('label').disable();
				this.form.get('value').disable();
				this.form.get('required').disable();
				break;
			
			case "checkbox":
				this.form.patchValue({
					type : 'checkbox',
				})
				break;
			
			case "input":
				this.form.patchValue({
					type : 'input',
					inputType : 'text',
				})
				break;

			case "radiobutton":
				this.form.patchValue({
					type : 'radiobutton',
				})
				break;

			case "select":
				this.form.patchValue({
					type : 'select',
				})
				break;
			
			case "subtitle":
				this.form.patchValue({
					type : 'subtitle',
					required : 'false',
					disabled : 'false',
				})
				this.form.get('value').disable();
				this.form.get('required').disable();
				this.form.get('disabled').disable();
				break;

			case "upload":
				this.form.patchValue({
					type : 'upload',
				})
				this.form.get('value').disable();
				this.form.get('dataSource').disable();
				break;

			case "integratedSearch":
				this.objectIntegratedSearch = new IntegratedSearch();
				this.form.patchValue({
					type : 'integratedSearch',
					required : 'false',
				})
				this.form.get('required').disable();
				break;

			case "text":
				this.form.patchValue({
					type : 'text',
					required : 'false',
				})
				this.form.get('required').disable();
				break;
			
			case "table":
				this.form.patchValue({
					type : 'table',
					required : 'false',
				})
				this.form.get('required').disable();
				break;

			default:
				break;
		}
	}

	public getFields(group: string): Field[] {
		return this.formFields.filter(el => el.type === group);
	}
	
	public getFieldsToIntegratedSearch(group: string): Field[] {
		const fields: Field[] = this.formFields.filter(field => field.type === group);
		return fields.filter(field2 => {

			if (!this.objectIntegratedSearch.returnIntegratedSearch) {
				this.objectIntegratedSearch.returnIntegratedSearch = [];
			}

			const index = this.objectIntegratedSearch.returnIntegratedSearch.map(el => el.nameField).indexOf(field2.name);

			if (index < 0) {
				return field2;
			}
		})
	}

	public getFieldsToOptionCondition(group: string): Field[] {
		const fields: Field[] = this.formFields.filter(field => field.type === group);
		return fields.filter(field2 => {
			const index = this.fieldOptionsCondition.map(el => el.fieldName).indexOf(field2.name);
			if (index < 0) {
				return field2;
			}
		})
	}

	public setName(event: any): void {
		const valueReceived: string = event.target.value;
		const newName = valueReceived.replace(/\W/gi, "")
		this.form.patchValue({
			name : newName
		})
	}

	public isPredefined(event: MatCheckboxChange): void {
		this.predefined = event.checked;
	}
	
	public isReadOnly(event: MatCheckboxChange): void {
		this.readonly = event.checked;
	}

	public setFieldPredefined(event: MatSelectChange): void {
		
		this.predefinedType = event.value;
		
		switch (event.value) {
			case 'cpf':
				this.form.patchValue({
					type : 'input',
					name : 'cpf',
					label: 'CPF',
					inputType : 'text',
					mask : '000.000.000-00',
				});
				break;
			
			case 'cnpj':
				this.form.patchValue({
					type : 'input',
					name : 'cnpj',
					label: 'CNPJ',
					inputType : 'text',
					mask : '00.000.000/0000-00',
				});
				break;
			
			case 'agencia':
				this.form.patchValue({
					type : 'input',
					name : 'agencia',
					label : 'Agência',
					inputType : 'text',
					mask : 'numbers',
					maxLength : '4',
				});
				break;
			
			case 'conta':
				this.form.patchValue({
					type : 'input',
					name : 'conta',
					label : 'Conta',
					inputType : 'text',
					mask : 'numbers',
					maxLength : '13',
				});
				break;

			case 'nomeRazaoSocial':
				this.form.patchValue({
					type : 'input',
					name : 'nomeRazaoSocial',
					label : 'Nome / Razão Social',
					inputType : 'text',
				});
				break;
		
			default:
				break;
		}
		this.form.get('name').disable(); 
		this.form.get('label').disable(); 
		this.form.get('inputType').disable(); 
		this.form.get('mask').disable(); 
		this.form.get('maxLength').disable(); 
		this.form.get('modifyDataEsteira').disable(); 
	}

	public setCurrentDate(event: MatCheckboxChange): void {
		this.currentDate = event.checked;
	}

	public showCBCurrentDate(): boolean {
		return this.form.value.mask === '00/00/0000 00:00' || this.form.value.mask === '00/00/0000';
	}

	public showMaxLenght(): boolean {
		return this.form.value.mask === '' || this.form.value.mask === 'numbers';
	}
	
	/** MÉTODOS REFERENTES AO COMPONENTE DE COMPARAÇÃO */
	public changeTypeComparation(event: MatRadioChange): void {
		if (event.value === 'custom') {
			this.formComparation.patchValue({
				target : ''
			})
		}
	}

	/********** MÉTODOS REFERENTES A CONFIGURAÇÃO DA PESQUISA INTEGRADA  **********/
	public setTypeIntegratedSearch(typeReceived: string): void {
		this.originalDataSourceIntegratedSearch.filter(type => {
			if (type.name === typeReceived) {
				this.fieldsIntegratedSearch = type.fields;
			}
		});
	}

	public addBindIntegratedSearch(): void {
		if (this.formIntegratedSearch.valid) {

			const data: ReturnIntegratedSearch = new ReturnIntegratedSearch();
			data.nameField = this.formIntegratedSearch.value.nameField;
			data.nameReturn = this.formIntegratedSearch.value.nameReturn;

			this.objectIntegratedSearch.returnIntegratedSearch = [...this.objectIntegratedSearch.returnIntegratedSearch, data];
			this.dataSource_integratedSearch = new MatTableDataSource<any>(this.objectIntegratedSearch.returnIntegratedSearch);
			this.formIntegratedSearch.get('nameField').reset();
			this.formIntegratedSearch.get('nameReturn').reset();
		}
	}

	public removeBindIntegratedSearch(bind: any): void {
		this.objectIntegratedSearch.returnIntegratedSearch = this.objectIntegratedSearch.returnIntegratedSearch.filter(
			element => element.nameField !== bind.nameField || element.nameReturn !== bind.nameReturn
		);
		this.dataSource_integratedSearch = new MatTableDataSource<any>(this.objectIntegratedSearch.returnIntegratedSearch);
	}

	/********** MÉTODOS REFERENTES A CRIAÇÃO DA TABELA  **********/
	public addColumn(): void {
		const dialogAddColumn: MatDialogRef<DialogAddColumnComponent, any> = this.dialog.open(DialogAddColumnComponent, {
			maxWidth: '96%',
			width: '96%',
			data : {
				formFields: this.formFields,
				editingField: false,
			}
		});

		dialogAddColumn.afterClosed().subscribe(
			(response: ColumnsTable) => {
				if (response) {
					response.name = `col-${this.columnsTable.length + 1}`;
					this.columnsTable = [...this.columnsTable, response];
				}
			}
		)
	}

	public editColumn(column: ColumnsTable) {

		let fields: Field[] = [];
		
		this.columnsTable.map(column => {
			fields = [...fields, column.field];
		});
		
		const dialogAddColumn: MatDialogRef<DialogAddColumnComponent, any> = this.dialog.open(DialogAddColumnComponent, {
			maxWidth: '96%',
			width: '96%',
			data : {
				formFields: fields,
				editingField: true,
				columnsTable: column,
			}
		});

		dialogAddColumn.afterClosed().subscribe(
			(response: ColumnsTable) => {
				if (response) {
					const i = this.columnsTable.findIndex(el => el.name === response.name);
					this.columnsTable[i] = response;
				} 
			}
		)
	}
	
	public deleteColumn(column: ColumnsTable) {
		const i = this.columnsTable.findIndex(el => el.name === column.name);
		this.columnsTable.splice(i, 1);
	}

	public dropColumnTable(event: CdkDragDrop<string[]>) {
		moveItemInArray(this.columnsTable, event.previousIndex, event.currentIndex);
	}
	
	/********** MÉTODOS REFERENTES A ADIÇÃO E REMOÇÃO DE OPÇÕES EM CAMPOS DO TIPO SELECT E RADIOBUTTON  **********/
	public addOption(): void {
		if (this.formOptions.valid) {
			const option: Option = new Option();
			option.label = this.formOptions.value.label;
			option.value = this.formOptions.value.value;

			if (this.fieldOptionsCondition.length > 0) {
				option.conditionOption = this.fieldOptionsCondition;
			}
			
			if (this.editOptionFlag) {
				this.fieldOptions[this.indexEditOptionFlag] = option;
			} else {
				this.fieldOptions = [...this.fieldOptions, option];
			}
			
			this.cancelEditionOption();
		}
	}

	public editOption(option: Option): void {
		this.editOptionFlag = true;

		this.fieldOptions.map((el, index) => {
			if (el.label === option.label && el.value === option.value) {
				this.indexEditOptionFlag = index;
			}
		})

		this.formOptions.patchValue({
			label : option.label,
			value : option.value
		});

		if (option.conditionOption) {
			this.fieldOptionsCondition = [...option.conditionOption];
		}
	}

	public cancelEditionOption(): void {
		this.formOptions.reset();
		this.formOptionsCondition.reset();
		this.fieldOptionsCondition = [];
		this.editOptionFlag = false;
	}

	public removeOption(option: Option): void {
		this.fieldOptions = this.fieldOptions.filter(element => element.value != option.value);
		this.form.patchValue({
			options : this.fieldOptions
		});

		if (this.editOptionFlag) {
			this.cancelEditionOption();
		}
	}

	public dropOption(event: CdkDragDrop<string[]>) {
		moveItemInArray(this.fieldOptions, event.previousIndex, event.currentIndex);
	}

	public setFieldToOptionCondition(event: any): void {
		const field = this.formFields.find(el => el.name === event);
		this.typeFieldOptionsCondition = field.type;
		this.optionsConditionAvailable = [];

		if (field.type === 'select' || field.type === 'radiobutton') {
			this.optionsConditionAvailable = [...field.options];
		}
	}

	public addOptionCondition(): void {
		if (this.formOptionsCondition.valid) {
			
			const field: Field = this.formFields.find(el => el.name === this.formOptionsCondition.value.fieldName);
			const optionCondition: ConditionOption = new ConditionOption();
			optionCondition.fieldName = this.formOptionsCondition.value.fieldName;

			if (this.setValueAsDataSource) {
				optionCondition.label = 'Valor do datasource';
				optionCondition.value = 'DATASOURCE';
			}

			if (field.type === "select" || field.type === "radiobutton") {
				optionCondition.label = this.formOptionsCondition.get('value').value['label'];
				optionCondition.value = this.formOptionsCondition.get('value').value['value'];
			}
			
			if (field.type === "checkbox") {
				optionCondition.label = this.formOptionsCondition.get('value').value === 'true' ? 'Selecionado' : 'Não Selecionado' ;
				optionCondition.value = this.formOptionsCondition.get('value').value;
			}

			if (field.type === "input" && !this.setValueAsDataSource) {
				optionCondition.label = this.formOptionsCondition.get('value').value || "(vazio)";
				optionCondition.value = this.formOptionsCondition.get('value').value;
			}

			this.fieldOptionsCondition = [...this.fieldOptionsCondition, optionCondition];
			this.setValueAsDataSource = false;
			this.formOptionsCondition.enable();
			this.formOptionsCondition.reset();
		}
	}

	public removeOptionCondition(optionCondition: ConditionOption): void {
		this.fieldOptionsCondition = this.fieldOptionsCondition.filter(element => (element.fieldName !== optionCondition.fieldName && element.value !== optionCondition.value))
	}

	public setDataSourceValue(event: MatCheckboxChange): void {
		this.setValueAsDataSource = event.checked;
		if (this.setValueAsDataSource) {
			this.formOptionsCondition.get('value').setValue('');
			this.formOptionsCondition.get('value').disable();
		} else {
			this.formOptionsCondition.get('value').enable();
		}
	}

	/********** MÉTODOS REFERENTES AS AÇÕES DOS BOTÕES  **********/
	public setButtonType(event: MatSelect): void {
		this.form.patchValue({
			buttonType : event.value
		})
	}

	public setActionButton(): void {
		let actionButton: ActionButton = new ActionButton();

		const buttonType = this.formButtons.value.buttonType;
		const decision = this.formButtons.value.decision;

		if (buttonType === "submit") {
			actionButton.decision = decision;
		}

		this.form.patchValue({
			actionButton : actionButton
		})
	}

	/********** MÉTODOS REFERENTES AS CONDIÇÕES PARA UM CAMPO SE TORNAR VISÍVEL  **********/
	public setFieldToConditionToVisible(event: MatSelectChange): void {
		this.conditionsToVisibleOptions = [];
		const field: Field = this.formFields.find(el => el.name === event.value);
		
		if (field.type === "select" || field.type === "radiobutton") {
			this.conditionsToVisibleOptions = [...field.options]
			this.showConditionToVisibleSelect = true;
			this.showConditionToVisibleInput = false;
			this.showConditionToVisibleCheckbox = false;
		} else if (field.type === "checkbox" || field.buttonType === "conditional") {
			this.showConditionToVisibleSelect = false;
			this.showConditionToVisibleInput = false;
			this.showConditionToVisibleCheckbox = true;
		} else {
			this.showConditionToVisibleSelect = false;
			this.showConditionToVisibleInput = true;
			this.showConditionToVisibleCheckbox = false;
		}
	}

	public addConditionToVisible(): void {

		const form: FormGroup = this.formConditionsToVisible;
		const field: Field = this.formFields.find(el => el.name === form.get('field').value);

		const condition: Condition = new Condition();
		condition.fieldName = field.name;
		condition.operator = form.get('operator').value;
		condition.fieldType = field.type;
		condition.value = form.get('value').value || '';

		if (field.type === "select" || field.type === "radiobutton") {
			condition.label = field.options.find(el => el.value === form.get('value').value).label;
		}

		if (this.utilService.isUnique(condition, this.fieldConditionsToVisible)) {
			this.fieldConditionsToVisible = [condition, ...this.fieldConditionsToVisible];
			this.dataSource_visible = new MatTableDataSource<Condition>(this.fieldConditionsToVisible);

			this.form.patchValue({
				conditionsToVisible : this.fieldConditionsToVisible
			})
			form.reset();
		} else {
			let dialog: Dialog = new Dialog();
			dialog.title = "Erro ao incluir Condição";
			dialog.message = "Já existe uma condição usando esse campo";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
		}
	}

	public deleteConditionToVisible(condition: Condition): void {
		this.fieldConditionsToVisible = this.fieldConditionsToVisible.filter((el => el !== condition));
		this.dataSource_visible = new MatTableDataSource<Condition>(this.fieldConditionsToVisible);
		this.form.patchValue({
			conditionsToVisible : this.fieldConditionsToVisible
		})
	}


	/********** MÉTODOS REFERENTES AS CONDIÇÕES PARA UM CAMPO SE TORNAR HABILITADO  **********/
	public setFieldToConditionToEnable(event: MatSelectChange): void {
		this.conditionsToEnableOptions = [];
		const field: Field = this.formFields.find(el => el.name === event.value);

		if (field.type === "select" || field.type === "radiobutton") {
			this.conditionsToEnableOptions = [...field.options]
			this.showConditionToEnableSelect = true;
			this.showConditionToEnableInput = false;
			this.showConditionToEnableCheckbox = false;

		} else if (field.type === "checkbox" || field.buttonType === "conditional") {
			this.showConditionToEnableSelect = false;
			this.showConditionToEnableInput = false;
			this.showConditionToEnableCheckbox = true;
		} else {
			this.showConditionToEnableSelect = false;
			this.showConditionToEnableInput = true;
			this.showConditionToEnableCheckbox = false;
		}
	}

	public addConditionToEnable(): void { 

		const form: FormGroup = this.formConditionsToEnable;
		const field: Field = this.formFields.find(el => el.name === form.get('field').value);

		const condition: Condition = new Condition();
		condition.fieldName = field.name;
		condition.operator = form.get('operator').value;
		condition.fieldType = field.type;
		condition.value = form.get('value').value || '';

		if (field.type === "select" || field.type === "radiobutton") {
			condition.label = field.options.find(el => el.value === form.get('value').value).label;
		}

		if (this.utilService.isUnique(condition, this.fieldConditionsToEnable)) {
			this.fieldConditionsToEnable = [condition, ...this.fieldConditionsToEnable];
			this.dataSource_enable = new MatTableDataSource<Condition>(this.fieldConditionsToEnable);

			this.form.patchValue({
				conditionsToEnable : this.fieldConditionsToEnable
			});

			form.reset();
		} else {
			let dialog: Dialog = new Dialog();
			dialog.title = "Erro ao incluir Condição";
			dialog.message = "Já existe uma condição usando esse campo";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
		}
	}

	public deleteConditionToEnable(condition: Condition): void {
		this.fieldConditionsToEnable = this.fieldConditionsToEnable.filter((el => el !== condition));
		this.dataSource_enable= new MatTableDataSource<Condition>(this.fieldConditionsToEnable);
		this.form.patchValue({
			conditionsToEnable : this.fieldConditionsToEnable
		});
	}


	/********** MÉTODOS REFERENTES A ADIÇÃO, EDIÇÃO E REMOÇÃO DE UM CAMPO  **********/
	public addField(nameField: string): void {

		let field: Field = new Field();
		field = this.form.value;
		
		if (this.form.valid) {
			field.predefined = this.predefined;
			field.currentDate = this.currentDate;
			field = this.adjustmentsField(field);

			if (this.validationField(field)) {
				field.name = !this.editingField ? this.setAdjustedName(field.name, field.type) : nameField;
				this.saveField.emit(field);
				this.resetForm();
			} 
		}
	}

	private validationField(field: Field): boolean {
		let noError: boolean[] = [];
		let dialog: Dialog = new Dialog();
		dialog.title = "Erro ao Adicionar Campo";
		dialog.type = DialogTypes.INFORMATION;

		if (this.editingField) {
			if (!this.isUniqueOnForm(field)) {
				noError = [...noError, false];
				dialog.message = "Já existe um campo com o mesmo nome no formulário.";
				this.showMessage(dialog);
			}
		}

		if (field.type === "button" && field.buttonType === "" ) {
			noError = [...noError, false];
			dialog.message = "Você precisa escolher o tipo do botão";
			this.showMessage(dialog);
		}
		
		if (field.type === "table" && field.dataSource === "" ) {
			noError = [...noError, false];
			dialog.message = "Para o componente tabela o campo 'datasource' é obrigatório";
			this.showMessage(dialog);
		}

		if (field.type === "table" && field.columnsTable.length === 0) {
			noError = [...noError, false];
			dialog.message = "Você precisa adicionar colunas a tabela.";
			this.showMessage(dialog);
		}

		if (field.type === "integratedSearch" && (
				this.formIntegratedSearch.value.searchType === '' ||
				this.formIntegratedSearch.value.criteria === '' ||
				this.objectIntegratedSearch.returnIntegratedSearch.length === 0
			)
		) {
			noError = [...noError, false];
			dialog.message = "Você precisa configurar a pesquisa integrada";
			this.showMessage(dialog);
		}

		if (field.required.toString() === "true" && field.visible.toString() === "false" ) {
			noError = [...noError, false];
			dialog.message = "Um campo que não é visível por padrão, não pode ser obrigatório.";
			this.showMessage(dialog);
		}

		if (noError.length === 0 && this.isTable && !this.isValid) {
			noError = [...noError, false];
			dialog.message = "Todos os campos obrigatórios precisam ser preenchidos.";
			this.showMessage(dialog);
		}

		return noError.length === 0;
	}

	private setAdjustedName(name: string, type: string): string {
		const nameLowerCase: string = name.toLowerCase();
		let codeType: string = "";

		switch (type) {
			case "button":
				codeType = "btn";
				break;
			
			case "checkbox":
				codeType = "chk";
				break;
			
			case "input":
				codeType = "txt";
				break;
			
			case "select":
				codeType = "ddl";
				break;
			
			case "radiobutton":
				codeType = "opt";
				break;
			
			case "subtitle":
				codeType = "sbt";
				break;
			
			case "upload":
				codeType = "upl";
				break;
			
			case "integratedSearch":
				codeType = "obj";
				break;
				
			case "table":
				codeType = "tab";
				break;
			
			case "text":
				codeType = "span";
				break;
			
			default:
				break;
		}

		return `${codeType}${nameLowerCase.replace(nameLowerCase.substring(0,1), nameLowerCase.substring(0,1).toUpperCase())}`;
	}

	private adjustmentsField(field: Field): Field {

		if (this.predefined) {
			switch (this.predefinedType) {
				case 'cpf':
					field.type = 'input';
					field.name = 'cpf';
					field.label = 'CPF';
					field.inputType = 'text';
					field.mask = '000.000.000-00';
					break;
				
				case 'cnpj':
					field.type = 'input';
					field.name = 'cnpj';
					field.label = 'CNPJ';
					field.inputType = 'text';
					field.mask = '00.000.000/0000-00';
					break;
			
				case 'agencia':
					field.type = 'input';
					field.name = 'agencia';
					field.label = 'Agência';
					field.inputType = 'text';
					field.mask = 'numbers';
					field.maxLength = '4';
					break;
			
				case 'conta':
					field.type = 'input';
					field.name = 'conta';
					field.label = 'Conta';
					field.inputType = 'text';
					field.mask = 'numbers';
					field.maxLength = '13';
					break;
			
				case 'nomeRazaoSocial':
					field.type = 'input';
					field.name = 'nomeRazaoSocial';
					field.label = 'Conta';
					field.inputType = 'text';
					field.mask = '';
					field.maxLength = '';
					break;
			
				default:
					break;
			}

		} else {

			if (this.editingField) {
				field.type = this.fieldInFocus;
			}

			if (field.type === "button") {
				field.required = false;
				field.dataSource = "";
				field.value = "false";
			} 
	
			if (field.type === "checkbox") {
				field.value = Boolean(field.value.toString() === "true" ? true : false);
			}
	
			if (field.type === "subtitle" || field.type === "text") {
				field.required = false;
				field.disabled = false;
			} 

			if (field.type === "integratedSearch") {
				this.objectIntegratedSearch.type = this.formIntegratedSearch.value.searchType;
				this.objectIntegratedSearch.criteria = this.formIntegratedSearch.value.criteria;
				field.integratedSearch = this.objectIntegratedSearch;
				field.mask = this.formIntegratedSearch.value.mask;
				field.required = false;
			}
						
			if (field.type === "table") {
				field.required = false;
				field.columnsTable = this.columnsTable;
			} 

			if (field.type === 'input') {
				if (field.disabled.toString() === 'false' && this.readonly) {
					field.readOnly = false;
				} else {
					field.readOnly = this.readonly
				}
			}

			if (field.type === 'select' || field.type === 'radiobutton') {
				field.options = this.fieldOptions;
			}

			if (field.type === 'comparation') {
				field.required = false;
				field.comparation = this.formComparation.value;
			}
			
		}

		return field;
	}

	public editField(field: Field): void {
		this.editingField = true;

		this.adjustFields(field.type);
		this.form.get('type').disable();
		this.form.get('name').disable();

		this.fieldOptions = [];
		this.fieldConditionsToVisible = [];
		this.fieldConditionsToEnable = [];

		this.fieldConditionsToVisible = this.normalizeCondition(field.conditionsToVisible);
		this.dataSource_visible = new MatTableDataSource(this.fieldConditionsToVisible);
		
		this.fieldConditionsToEnable = this.normalizeCondition(field.conditionsToEnable);
		this.dataSource_enable = new MatTableDataSource(this.fieldConditionsToEnable);

		this.form.patchValue({
			type : field.type,
			name : field.name,
			visible : field.visible,
			disabled : field.disabled,
			dataSource : field.dataSource,
			required : field.required,
			label: field.label,
			value : field.value,
			cssGrid : field.cssGrid,
			cssClass : field.cssClass,
			options : [...field.options],
			conditionsToVisible : [...this.fieldConditionsToVisible],
			conditionsToVisibleSatisfy : this.fieldConditionsToVisible.length > 0 ? this.normalizeSatisfy(field.conditionsToVisibleSatisfy): '',
			conditionsToEnable : [...this.fieldConditionsToEnable],
			conditionsToEnableSatisfy : this.fieldConditionsToEnable.length > 0 ? this.normalizeSatisfy(field.conditionsToEnableSatisfy) : '',
			inputType : field.inputType,
			selectMultiple: field.type === 'select' ? this.normalizeSelectMultiple(field.selectMultiple.toString()) : '',
			mask : field.mask,
			currentDate : field.currentDate,
			maxLength : field.maxLength,
			buttonType : field.buttonType,
			actionButton : field.actionButton,
			modifyDataEsteira: field.modifyDataEsteira
			
		});

		if (field.actionButton && field.actionButton.decision) {
			this.formButtons.patchValue({
				buttonType : field.buttonType,
				decision : field.actionButton.decision,
			})
		}

		if (field.type === "button") {
			this.formButtons.patchValue({
				buttonType : field.buttonType,
			})
		}

		if (field.type === "integratedSearch") {
			this.setTypeIntegratedSearch(field.integratedSearch.type);
			this.formIntegratedSearch.patchValue({
				searchType : field.integratedSearch.type,
				criteria : field.integratedSearch.criteria,
				mask : field.mask,
			});
			this.formIntegratedSearch.enable();
			this.objectIntegratedSearch.type = field.integratedSearch.type;
			this.objectIntegratedSearch.returnIntegratedSearch = field.integratedSearch.returnIntegratedSearch;
			this.dataSource_integratedSearch = new MatTableDataSource<any>(field.integratedSearch.returnIntegratedSearch);
		}

		if (field.type === "table") {
			this.columnsTable = field.columnsTable;
		}

		if (field.type === 'input') {
			this.readonly = field.readOnly.toString() === 'true' ? true : false;
		}

		if (field.type === 'select' || field.type === 'radiobutton') {
			this.fieldOptions = [...field.options];
		}

		if (field.type === 'comparation') {
			this.formComparation.patchValue({
				source : field.comparation.source,
				operator : field.comparation.operator,
				compareWith : field.comparation.compareWith,
				target : field.comparation.target
			})
		}

		this.fieldInFocus = field.type;
	}

	public cancelEdition(): void {
		this.editingField = false;
		this.fieldOptions = [];
		this.fieldConditionsToVisible = [];
		this.fieldConditionsToEnable = [];
		this.resetForm();

		if (this.isTable) {
			this.cancelEditionField.emit();
		} 
	}

	private resetForm(): void {
		this.form.patchValue({
			type : '',
			name : '',
			dataSource : '',
			visible : 'true',
			disabled : 'false',
			required : 'false',
			label : '',
			value : '',
			cssGrid : 'col-12',
			cssClass : '',
			options : [],
			conditionsToVisible : [],
			conditionsToEnable : [],
			inputType : '',
			mask : '',
			buttonType : '',
			actionButton : '',
		});
		
		this.fieldConditionsToVisible = [];
		this.fieldConditionsToEnable = [];
		this.dataSource_visible = new MatTableDataSource();
		this.dataSource_enable = new MatTableDataSource();
		this.dataSource_integratedSearch = new MatTableDataSource();
		this.form.enable();
		this.formButtons.reset();
		this.formConditionsToEnable.reset();
		this.formConditionsToVisible.reset();
		this.formIntegratedSearch.reset();
		this.predefined = false;
		this.currentDate = false;
		this.objectIntegratedSearch = undefined;
		this.columnsTable = [];
		this.fieldInFocus = undefined;
		this.readonly = false;
		this.fieldOptions = [];
	}

	private isUniqueOnForm(field: Field): boolean {
		const indexOf: number = this.formFields.map(el => el.name).indexOf(field.name);
		return indexOf >= 0 ? false : true;
	}

	private normalizeCondition(conditions: Condition[]): Condition[] {
		conditions.map(condition => {
			if (!condition.fieldType) {
				const field: Field = this.formFields.find(el => el.name === condition.fieldName);
				condition.fieldType = field.type;
			}
			
			if (!condition.operator) {
				condition.operator = "equal";
			}
		});

		return conditions;
	}

	private normalizeSatisfy(value: string): string {
		return value ? value : 'all';
	}

	private normalizeSelectMultiple(value: string): string {
		return value ? value : 'false';
	}


	/********** MÉTODOS AUXILIARES  **********/
	public showMessage(dialog: Dialog): void {
		this.dialogRef = this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title : dialog.title,
				message : dialog.message,
				type : dialog.type,
			}
		});
	}
}
