import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MessengerService } from 'src/app/services/messenger.service';
import { Field } from 'src/app/models/template.model';

@Component({
	selector: 'app-button',
	templateUrl: './button.component.html',
	styleUrls: ['./button.component.scss']
})
export class ButtonComponent implements OnInit {
	
	public field: Field;
	public group: FormGroup;
	public class: string = "btn";
	public wrapClass: string = "text-center ";
	
	constructor(
		private messengerService: MessengerService
	) { }
	
	ngOnInit() {
		this.field.visible = Boolean(this.field.visible.toString() === "true");
		this.field.disabled = Boolean(this.field.disabled.toString() === "true");
		this.field.required = Boolean(this.field.required.toString() === "true");
		
		if (this.field.cssClass !== "") {
			this.wrapClass += this.field.cssClass;
		}
		
		if (this.field.buttonType === "submit") {
			if (this.field.actionButton.decision === "5") {
				this.class += " btn-success";
			} else if (this.field.actionButton.decision === "4") {
				this.class += " btn-danger";
			} else if (this.field.actionButton.decision === "3") {
				this.class += " btn-warning";
			} else if (this.field.actionButton.decision === "8") {
				this.class += " btn-info";
			} else if (this.field.actionButton.decision === "6") {
				this.class += " btn-primary";
			}
		}  else if (this.field.buttonType === "conditional") {
			this.class += " btn-secondary";
		}
	}
	
	public click(){
		
		this.messengerService.dispatchAction(this.field.name, this.field);

		if (this.field.buttonType === "conditional") {
			this.field.value = this.field.value === "true" ? "false" : "true";
			this.group.get(this.field.name).setValue(this.field.value);

			const pattern = /active/;
			if (pattern.test(this.class)) {
				this.class = "btn btn-secondary"
			} else {
				this.class = "btn btn-secondary active"
			}
		}
	}
}
