import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';
import { ManagerialView } from 'src/app/models/managerial-view.interface';
import { ResponseList,  } from 'src/app/models/response-list.interface';


@Injectable({ providedIn: 'root' })
export class ServiceCenterBaixaService {

    private urlServiceCenter = "/api/v1/service-center";

    constructor (
        private http: HttpClient,
    ) {}
    
    public getInformacaoConta(agencia: string, contaCorrente: string, documento: string): Observable<any> {
        return this.http.get<any>(`${environment.urlServices}${this.urlServiceCenter}/informacao-conta`, {
            params : {
                agencia: agencia,
                contaCorrente: contaCorrente,
                documento: documento
            }
        })
    }

    public getLocalizaCarga(agencia: string, contaCorrente: string, atividadeId: string): Observable<any> {
        return this.http.get<any>(`${environment.urlServices}${this.urlServiceCenter}/localiza-carga`, {
            params : {
                agencia: agencia,
                contaCorrente: contaCorrente,
                atividadeId: atividadeId
            }
        })
    }

    public setEsteiraAnalise(data: any): Observable<any> {
        return this.http.get<any>(`${environment.urlServices}${this.urlServiceCenter}/localiza-carga`, {
            params : {
                data: data
            }
        })
    }

}