import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
	selector: 'app-dialog',
	templateUrl: './dialog-request-review.component.html',
	styleUrls: ['./dialog-request-review.component.scss']
})
export class DialogRequestReviewComponent implements OnInit {

	public form: FormGroup;

	constructor(
		public dialogRef: MatDialogRef<DialogRequestReviewComponent>,
		private fb: FormBuilder,
	) { }
	
	ngOnInit() {
		this.form = this.fb.group({
			justificativa : ['', Validators.required],
			filaId : ["90010100", Validators.required]
		})
	}

	public send() {
		if (this.form.valid) {
			this.dialogRef.close(this.form.value);
		}
	}

}
