import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatTableDataSource, MatPaginator, PageEvent } from '@angular/material';
import { ListHistory } from 'src/app/models/dialog.model';
import { HistoricoService } from '../../services/historico.service';

@Component({
	selector: 'app-dialog-list-history',
	templateUrl: './dialog-list-history.component.html',
	styleUrls: ['./dialog-list-history.component.scss']
})
export class DialogListHistoryComponent implements OnInit {

	public displayedColumns: string[] = ['item', 'dataAtendimento', 'status', 'usuario', 'atividade', 'atendido', 'imagens'];
	public dataSource = new MatTableDataSource<any>([]);
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public collection: any[] = [];
	public length: number;
	public pageSize = 4;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [4];
	public loading: boolean = false;

	constructor(
		public dialogRef: MatDialogRef<DialogListHistoryComponent>,
		@Inject(MAT_DIALOG_DATA) public data: ListHistory,
		private historyService: HistoricoService
	) { }
	
	ngOnInit() {
		this.getBoxes();
	}

	public openImages(docNumber:string, galleries:string): void{
		const displayImages = {
			document: docNumber,
			images: galleries
		}
		localStorage.setItem('displayImages', JSON.stringify(displayImages));
		window.open('/#/operacao/exibir-imagens');
	}


	private getBoxes(): void {
		this.loading = true;
		this.collection = [];
		this.historyService.get(this.data.documento, this.data.agencia, this.data.contaCorrente).subscribe(
			response => {
				this.collection = [...response.data];
				this.length = this.collection.length;
				this.collection.forEach((element, index) => {
					element['position'] = this.length - index;
				});
				this.loading = false;
				this.filterPaginator();
				this.dataSource.paginator = this.paginator;
			}
		);
	}

	private filterPaginator() {
		this.loading = true;
		let arrPaginado = this.collection.filter(
			value => value.position <= this.length-(this.pageIndex * this.pageSize) 
					&& value.position > this.length-((this.pageIndex * this.pageSize) + this.pageSize));
		this.dataSource = new MatTableDataSource<any>(arrPaginado);
		
		this.loading = false;
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.filterPaginator();
	}

	public decision(decision: string) {
		this.dialogRef.close(decision);
	}
}
