import { Injectable } from '@angular/core';


@Injectable({
	providedIn: 'root'
})
export class UtilsService {
    
    adjustMask(mask: string, value: string): string {
        switch (mask) {
            case "00/00/0000":
                return value.replace(/(\d{2})(\d{2})(\d{4})/g, "$1/$2/$3");
            
            case "00/00/0000 00:00":
                return value.replace(/(\d{2})(\d{2})(\d{4})(\d{2})(\d{2})/g, "$1/$2/$3 $4:$5");
            
            case "separator.2":
                let a = value.split('.')
                let b = '';
                a[0].split("").reverse().map((el, index) => { 
                    if ((index+1) % 3 !== 0 || (index+1) === value.length) {
                        b = el + b;
                    } else {
                        b = "." + el + b;
                    }
                });

                return 'R$ '+ b + (a[1] ? ','+ a[1] : ( b ? ',00' : ''));
      
            default:
                return value
        }
    }
}