import { Usuario } from './usuario.interface';

export class Dialog {
    title: string;
    message: string;
    type: string;
    tagIds: string[];
    activities: ActivityOpenTicket[];
    etiqueta: string;
    acao: string;
    users: Usuario[];
}

export interface ActivityOpenTicket {
    id: number;
    nome: string;
}

export interface UserContingency {
    email: string;
    id: string;
    login: string;
    matricula: string;
    nomeCompleto: string;
    perfil: string;
    status: string;
    statusId: number;
}

export interface ListHistory {
    agencia: string;
    documento: string;
    contaCorrente: string;
}