import { Criteria } from './data-source-integrated-search.interface';

export class Template {
    id: number;
    name: string;
    header: Header;
    actions?: Action[];
    steps: Step[];
    decision: Decision;
    rejectionReasons: RejectionReasons[];
    contingencyReasons: ContingencyReasons[];
    templateVersaoId: number;
}

export class Decision {
    buttons: Field[];
}

export interface Action {
    value: number;
    name: string;
    checked: boolean;
}

export interface Header {
    docNumber: string;
    bankBranch: string;
    bankAccount: string;
    indexador: string;
}

export class Step {
    index: number;
    name: string;
    fieldGroups: FieldGroup[];
}

export class FieldGroup {
    index: number;
    name: string;
    fields: Field[];
}

export class Field {
    type: string;
    name: string;
    dataSource: string;
    visible: boolean | string;
    required?: boolean | string;
    disabled?:boolean | string;
    cssGrid?: string;
    cssClass?: string;
    value?: any;
    label?: string;
    inputType?: string;
    buttonType?: string;
    selectMultiple?: boolean | string;
    options?: Option[];
    conditionsToVisible?: Condition[];
    conditionsToVisibleSatisfy?: string;
    conditionsToEnable?: Condition[];
    conditionsToEnableSatisfy?: string;
    actionButton?: ActionButton;
    mask?: string;
    maxLength?: string;
    predefined?: boolean | string;
    currentDate?: boolean | string;
    integratedSearch?: IntegratedSearch;
    columnsTable?: ColumnsTable[];
    readOnly?: boolean | string;
    comparation?: Comparation;
    modifyDataEsteira?: string;
}

export class Option {
    label: string;
    value: any;
    conditionOption: ConditionOption[];
}

export class ConditionOption {
    fieldName: string;
    label: string;
    value: string;
}

export class Condition {
    fieldName: string;
    label: string;
    operator: string;
    fieldType: string;
    value: any;
}

export class ActionButton {
    decision?: string;
}

export class RejectionReasons {
    checked: boolean;
    name: string;
    value: number;
}

export class ContingencyReasons {
    checked: boolean;
    name: string;
    value: number;
}

export class IntegratedSearch {
    type: string;
    criteria: Criteria[];
    returnIntegratedSearch: ReturnIntegratedSearch[];
}

export class ReturnIntegratedSearch {
    nameField: string;
    nameReturn: string;
}

export class ColumnsTable {
    name: string;
    label: string;
    field : Field;
}

export class Comparation {
    source: string;
    operator: string;
    compareWith: string;
    target: string;
}
