import { Component, OnInit, Input, ViewChild, OnDestroy } from '@angular/core';
import { CropperComponent } from 'angular-cropperjs';


@Component({
	selector: 'app-image-editor',
	templateUrl: './image-editor.component.html',
	styleUrls: ['./image-editor.component.scss']
})

export class ImageEditorComponent implements OnInit, OnDestroy {
	@ViewChild('Imgcropper', { static: true }) public Imgcropper: CropperComponent;

	@Input() imageEditor: any;

	public config: any;
	public indice: number;
	public loading: boolean = false;
	public disabledToolbar:boolean = true;
	public image: string;
	public generalCont: number;
	
	constructor() { }
	
	ngOnInit() {
		this.config = {
			autoCrop: false,
			background: true,
			movable: true,
			rotatable: true,
			scalable: true,
			dragMode: 'none',
			zoomable: true,
			checkImageOrigin: false,
			highlight: true,
			ready: ()=>{
				this.loading = false;
				this.disabledToolbar = false;
			},
			hasSameSize: false
		};
		this.indice = this.imageEditor.indiceSelected;
		const milisegundos = new Date().getMilliseconds();
		this.generalCont = milisegundos;
		this.image = this.imageEditor.images[this.imageEditor.indiceSelected].url +'?order='+ this.generalCont;
	}

	ngOnDestroy() {
		this.Imgcropper.cropper.destroy();
	}

	public dragMove() {
		this.Imgcropper.cropper.setDragMode('move');
	}

	public dragCrop() {
		this.Imgcropper.cropper.setDragMode('crop');
	}

	public zoom(value: number) {
		this.Imgcropper.cropper.zoom(value);;
	}

	public rotate() {
		this.Imgcropper.cropper.rotate(45);
	}

	public crop() {
		this.Imgcropper.cropper.getCroppedCanvas().toBlob(blob=>{
			const newBlob = new Blob([blob], {type: "image/png"});
			const fileURL = window.URL.createObjectURL(newBlob);
			window.open(fileURL);
		});
	}

	public reset() {
		this.Imgcropper.cropper.clear();
		this.Imgcropper.cropper.setDragMode('move');
		this.Imgcropper.cropper.reset();
	}

	public clear() {
		this.Imgcropper.cropper.clear()
	}

	public lastImage(){
		if(this.indice > 0) {
			this.indice --;
			this.generalCont++;
			this.image = this.imageEditor.images[this.indice].url +'?order=' + this.generalCont;
			this.Imgcropper.cropper.destroy();

		}
	}

	public nextImage() {
		if(this.indice < ( this.imageEditor.images.length - 1)) {
			this.indice ++;
			this.generalCont++;
			this.image = this.imageEditor.images[this.indice].url +'?order=' + this.generalCont;
			this.Imgcropper.cropper.destroy();
		}
	}

}
