import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';
import { ActivityOpenTicket } from 'src/app/models/dialog.model';
import { OpenTicket, Analyse } from 'src/app/models/post-analyse.class';
import { ResponseList } from 'src/app/models/response-list.interface';
import { PostManualSorting } from 'src/app/models/post-manual-sorting.model';
import { EsteiraHistorico } from 'src/app/models/esteira-historico.interface';


@Injectable({ providedIn: 'root' })
export class EsteiraService {
    private urlEsteiras: string = '/api/v1/esteiras';

    constructor (
        private http: HttpClient
    ) {}

    public getNextItem(): Observable<any> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/esteiras`).pipe(
            map(response => response.data)
        )
    }

    public upload(file: any): Observable<any> {
        return this.http.post<Observable<any>>(`${environment.urlServices}/api/v1/shared/upload`, file)
    }

    public uploadConvert(files: any, format: string): Observable<any> {
        return this.http.post<Observable<any>>(`${environment.urlServices}/api/v1/shared/upload-files-convert/${format}`, files)
    }

    public postAnalyse(body: Analyse, esteiraId: string): Observable<any> {
        return this.http.put<Observable<any>>(`${environment.urlServices}/api/v1/esteiras/${esteiraId}/finaliza`, body)
    }
    
    public postManualSorting(body: PostManualSorting, esteiraId: string): Observable<any> {
        return this.http.put<Observable<any>>(`${environment.urlServices}/api/v1/triagens-manuais/${esteiraId}`, body)
    }

    public getStaticOperations(): Observable<ActivityOpenTicket[]> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/atividades/operacao-estatica`).pipe(
            map(response => response.data)
        );
    }

    public getTemplateToStaticOperation(idActivity: number): Observable<any> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/atividades/${idActivity}/template`).pipe(
            map(response => response.data)
        )
    }

    public postOpenTicket(body: OpenTicket): Observable<any> {
        return this.http.post<Observable<any>>(`${environment.urlServices}/api/v1/esteiras/chamado`, body)
    }

	public findAnalyzes(
        typeConsult: string, 
        atividadeId: string, 
        dataInicial: string, 
        dataFinal: string, 
        documento: string,
        numeroProcesso: string, 
        numeroPasta: string, 
        statusId: string, 
        pageIndex: number, 
        pageSize: number
    ): Observable<ResponseList> {

        if (!statusId){
            statusId = "";
        }

		return this.http.get<ResponseList>(`${environment.urlServices}/api/v1/analises`, {
			params : {
                TipoConsulta : typeConsult, 
                AtividadeId : atividadeId,
				DataInicial : dataInicial,
				DataFinal : dataFinal,
                Documento : documento,
                NumeroProcesso : numeroProcesso,
                NumeroPasta : numeroPasta,
                StatusId : statusId,
				PageIndex : pageIndex.toString(),
				PageSize : pageSize.toString()
			}
		});
    } 

    public findHistoric(
        typeConsult: string, 
        agencia: string,
        conta: string, 
        documento: string,
        pageIndex: string, 
        pageSize: string
    ): Observable<ResponseList> {       
		return this.http.get<ResponseList>(`${environment.urlServices}/api/v1/analises`, {
			params : {
                TipoConsulta : typeConsult, 
                Agencia : agencia,
				ContaCorrente : conta,
				Documento : documento,
				PageIndex : pageIndex,
				PageSize : pageSize
			}
		});
    }

    public findHistoricByProcessOrFolder(
        typeConsult: string, 
        numeroProcesso: string,
        numeroPasta: string,
        pageIndex: string, 
        pageSize: string
    ): Observable<ResponseList> {       
		return this.http.get<ResponseList>(`${environment.urlServices}/api/v1/analises`, {
			params : {
                TipoConsulta : typeConsult, 
                NumeroProcesso : numeroProcesso,
				NumeroPasta : numeroPasta,
				PageIndex : pageIndex,
				PageSize : pageSize
			}
		});
    }

    public finalizeContingencie(esteiraId: number, body: any): Observable<any> {
        return this.http.put<any>(`${environment.urlServices}/api/v1/esteiras/${esteiraId}/finaliza-contingencia`, body)
    }
    
    public getAnalyse(esteiraId: string): Observable<any> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/analises/${esteiraId}`)
    }

    public putUpdateActivity(esteiraId: number, atividadeId: number): Observable<any> {
        const data = {
            esteiraId : esteiraId,
            atividadeId : atividadeId
        }
        return this.http.put<any>(`${environment.urlServices}/api/v1/triagens-manuais/AlteraAtividadeEsteira`, data).pipe(
            map(response => response.data)
        )
    }

    public requestReview(esteiraId: string, body: any): Observable<any> {
        return this.http.put<any>(`${environment.urlServices}/api/v1/esteiras/${esteiraId}/revisao`, body)
    }

    public getHistoric(esteiraId: string): Observable<EsteiraHistorico[]> {
        return this.http.get<any>(`${environment.urlServices}/api/v1/esteiras/${esteiraId}/logs`).pipe(
            map( response => response.data)
        )
    }

    public getEsteirasPrioritarias(): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices + this.urlEsteiras + "/esteiraPrioritaria");
    }

    public getAnaliseAnexos(esteriraId: string) {
		return this.http.get<ResponseList>(environment.urlServices + '/api/v1/analises/anexos/' + esteriraId);
    }
    
    public getEsteirasLogs(params: any): Observable<any> {
		return this.http.get<any>(environment.urlServices + this.urlEsteiras + "/logs",{
            params
        });
    }

    public changeEsteira(esteiraId: string, atividadeId: string, fromAnalise: boolean): Observable<any> {
        return this.http.put<any>(`${environment.urlServices}/api/v1/esteiras/${esteiraId}/encaminhar/${atividadeId}`, null);
    }

    public getSla(body: any): Observable<any> {
        let data: object;
        if(body.ExportaXLSX) {
            data = {
                responseType : 'blob' as 'json',
                params : body,
                observe : 'response'
            }
        } else {
            data = {
                params : body
            }
        }
		return this.http.get<any>(`${environment.urlServices + this.urlEsteiras}/fora-de-sla`, data)
    }
    
    public putPriorizarEsteira(esteiraId: string, usuarioId: string): Observable<any> {
        return this.http.put<any>(`${environment.urlServices + this.urlEsteiras}/${esteiraId}/usuario/${usuarioId}/priorizar`, null);
    }

}