import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatTableDataSource, MatPaginator, MatSnackBar } from '@angular/material';
import { HistoricoKit,EsteiraLog } from 'src/app/models/historico-kit.interface';
import { HistoricoService } from '../../services/historico.service';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';

@Component({
	selector: 'app-dialog-list-history',
	templateUrl: './dialog-historico-kit.component.html',
	styleUrls: ['./dialog-historico-kit.component.scss']
})
export class DialogHistoricokitComponent implements OnInit {

	public EsteiraLogDisplayedColumns: string[] = ['nome', 'detalhes', 'dataCadastro', 'criadoPor'];
	public dataSource = new MatTableDataSource<EsteiraLog>([]);
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public collection: any[] = [];
	public historicoKit: HistoricoKit;
	public loading: boolean = false;
	

	constructor(
		public dialogRef: MatDialogRef<DialogHistoricokitComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private historyService: HistoricoService,
		private matSnackBar: MatSnackBar,
	) { }
	
	ngOnInit() {
		this.getHistorico();
	}


	private getHistorico(): void {
		this.loading = true;
		this.collection = [];
		this.historyService.getHistoricoKit(this.data.esteiraId).subscribe(
			response => {
				this.historicoKit = response.data;
				this.dataSource.data = this.historicoKit.etapa.esteiraLog;
				this.loading = false;
			}, 
			error => {
				this.loading = false;
				this.close();
				SnackbarComponent.showError(this.matSnackBar, error);
			}
		);
	}

	public close() {
		this.dialogRef.close();
	}

}
