import { ButtonSubmitDecisions, ButtonType } from 'src/app/models/button-type.enum';
import { FieldType } from 'src/app/models/field.type.enum';
import { Operators } from 'src/app/models/operator.enum';
import { Condition } from 'src/app/models/template.model';
import { CssGrid } from 'src/app/models/css-grid.enum';

export class ConfigurationServiceUtilService {

	public buttonTypes: string[][];
    public buttonSubmitDecisions: string[][] = [];
	public fieldTypes: string[][] = [];
	public operators: string[][] = [];
	public cssGrid: string[][];

    constructor() {
		this.buttonTypes = Object.entries(ButtonType);
        this.buttonSubmitDecisions = Object.entries(ButtonSubmitDecisions);
		this.fieldTypes = Object.entries(FieldType);
		this.operators = Object.entries(Operators);
		this.cssGrid = Object.entries(CssGrid);
    }

    public getFriendlyType(type: string): string {
		return type ? this.fieldTypes.find(el => el[0] === type)[1] : "";
	}

	public getFriendlyOperator(type: string): string {
		return type ? this.operators.find(el => el[0] === type)[1] : "";
	}

	public getFriendlyValue(element: Condition): string {
		if (element.fieldType === 'select') {
			return element.label +" (Valor: "+ element.value +")";
		} else if (element.fieldType === 'checkbox') {
			if (element.value === 'true') {
				return 'Selecionado';
			} else {
				return 'Não Selecionado'
			}
		} else {
			if (element.value === "") {
				return "(vazio)";
			} else {
				return element.value;
			}
		}
	}

	public getFriendlyButtonType(type: string): string {
		return this.buttonTypes.find(el => el[0] === type)[1];
	}
	
	public getFriendlyButtonSubmitDecisions(type: string): string {
		return this.buttonSubmitDecisions.find(el => el[0] === type)[1];
	}

	public getFriendlyBoolean(value: string): string {
		return value === "true" ? "Sim" : "Não"
	}

	public getFriendlyWidht(type: string): string {
		return this.cssGrid.find(el => el[0] === type)[1];
	}

	public isUnique(condition: Condition, collection: Condition[]): boolean {
		const temp: Condition = collection.find(el => { 
			return el.fieldName === condition.fieldName && el.operator === condition.operator && el.value === condition.value
		});

		if (temp === undefined) {
			return true;
		} else {
			return false;
		}
	}
}