import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatTableDataSource, MatPaginator } from '@angular/material';
import { EsteiraHistorico } from 'src/app/models/esteira-historico.interface';

@Component({
	selector: 'app-dialog',
	templateUrl: './dialog-historic.component.html',
	styleUrls: ['./dialog-historic.component.scss']
})
export class DialogHistoricComponent implements OnInit {

	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public dataSource = new MatTableDataSource<EsteiraHistorico>();
	public displayedColumns: string[] = ['dataCadastro', 'nome', 'nomeCompleto'];

	/** Paginator */
	public length: number = null;
	public pageSize: number = 5;
	public pageIndex: number = 0;

	constructor(
		public dialogRef: MatDialogRef<DialogHistoricComponent>,
		@Inject(MAT_DIALOG_DATA) public data: EsteiraHistorico[],
	) { }
	
	ngOnInit() {
		this.dataSource = new MatTableDataSource<EsteiraHistorico>(this.data);
		this.dataSource.paginator = this.paginator;
		this.length = this.data.length;
	}

	public close() {
		this.dialogRef.close();
	}

}
