import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { SharedModule } from 'src/app/shared/shared.module';
import { AutenticationService } from './services/autentication.service';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MAT_DATE_LOCALE, MatPaginatorIntl ,MatSidenavModule, MatToolbarModule, MatMenuModule, MatIconModule } from '@angular/material';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { GeneralInformationsService } from './services/general-informations.service';
import { AuthInterceptor } from './auth.interceptor';
import { NgxMaskModule } from 'ngx-mask';
import { CustomPaginator } from './custom-paginator';

@NgModule({
	declarations: [
		AppComponent,
	],
	imports: [
		BrowserAnimationsModule,
		BrowserModule,
		SharedModule,
		HttpClientModule,
		AppRoutingModule,
		NgxMaskModule.forRoot(),
		MatSidenavModule,
		MatToolbarModule,
		MatMenuModule,
		MatIconModule
	],
	exports: [

	],
	providers: [
		{provide: MAT_DATE_LOCALE, useValue: 'pt-BR'},
		{provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true },
		AutenticationService,
		GeneralInformationsService,
		{ provide: MatPaginatorIntl, useClass: CustomPaginator }
	],
	bootstrap: [AppComponent]
})
export class AppModule { }
