import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { ResponseList } from '../models/response-list.interface';
import { PauseTypes } from '../models/pause-types.interface';
import { map } from 'rxjs/operators';
import { Usuario } from '../models/usuario.interface';
import { DataSourceIntegratedSearch, PostIntegratedSearch } from '../models/data-source-integrated-search.interface';


@Injectable({
	providedIn: 'root'
})
export class GeneralInformationsService {
	
	constructor(
		private http: HttpClient,
	) { }
	
	public getTypeOfDocuments(): Observable<ResponseList> {
		return this.http.get<ResponseList>(environment.urlServices +'/api/v1/grupos-arquivamento')
	}

	public getPauseTypes(): Observable<PauseTypes[]> {
		return this.http.get<any>(environment.urlServices +'/api/v1/tipo-pausas').pipe(
			map(response => response.data)
		);
	}

	public getUsersByLine(filaId: number): Observable<Usuario[]> {
		return this.http.get<any>(environment.urlServices +`/api/v1/usuarios/filas/${filaId}`).pipe(
			map(response => response.data)
		)
	}

	public getDataSourcesIntegratedSearch(): Observable<DataSourceIntegratedSearch[]> {
		return this.http.get<any>(environment.urlServices +`/api/v1/shared/datasources`).pipe(
			map(response => response.data)
		);
	}

	public getDateIntegratedSearch(body: PostIntegratedSearch): Observable<any> {
		return this.http.post<any>(environment.urlServices +`/api/v1/shared/pesquisa-integrada`, body).pipe(
			map(response => response.data)
		);
	}
}
