import { Router } from '@angular/router';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { throwError, Observable, BehaviorSubject } from 'rxjs';
import { shareReplay, tap, catchError, } from "rxjs/operators";
import * as moment from 'moment';
import * as jwt_decode from "jwt-decode";
import { User } from '../models/user.model';
import { element } from 'protractor';
import { PermissionTypes } from '../models/permission-types.enum';

@Injectable()
export class AutenticationService {
    public $loggedSubject: BehaviorSubject<boolean>;
    public $logged: Observable<boolean>;
    public $permittedSubject: BehaviorSubject<boolean>;
    public $permitted: Observable<boolean>;

    constructor(
        private router: Router,
        private http: HttpClient,
    ) {
        this.$loggedSubject = new BehaviorSubject<boolean>(this.isLoggedIn());
        this.$logged = this.$loggedSubject.asObservable();
    }

    public login(user: any): Observable<any> {
        //TO DO
        const params: URLSearchParams = new URLSearchParams();
        params.set('grant_type', 'password');
        params.set('client_id', environment.clientId);
        params.set('client_secret', environment.clientSecret);
        params.set('scope', '');
        params.set('username', user.username);
        params.set('password', user.password);
        params.set('domain', user.domain);

        return this.SendAuthentication(params);
    }

    private SendAuthentication(params: URLSearchParams): Observable<any> {
        const options = {
            headers: new HttpHeaders()
                .set("Content-Type", "application/x-www-form-urlencoded")
        }
        return this.http.post<any>(`${environment.urlServices}/connect/token`, params.toString(), options)
            .pipe(
                catchError(error => {
                    this.router.navigateByUrl('/');
                    return throwError(error);
                }
                ),
                shareReplay()
                , tap(res => this.setSession(res))
            );
    }

    private setSession(authResult) {
        const expiresAt = moment().add(authResult.expires_in, 'second');
        sessionStorage.setItem('refresh_token', authResult.refresh_token);
        sessionStorage.setItem('id_token', authResult.access_token);
        sessionStorage.setItem("expires_at", JSON.stringify(expiresAt.valueOf()));
        const userInfo: any = jwt_decode(authResult.access_token);
        const user: User = new User(
            userInfo["http://schemas.xmlsoap.org/ws/2005/05/identity/claims/name"],
            userInfo["http://schemas.microsoft.com/ws/2008/06/identity/claims/role"],
            userInfo["http://schemas.xmlsoap.org/ws/2005/05/identity/claims/email"],
            userInfo["http://schemas.xmlsoap.org/ws/2005/05/identity/claims/givenname"],
            userInfo["http://schemas.xmlsoap.org/ws/2005/05/identity/claims/surname"]
        )
        sessionStorage.setItem('user', btoa(JSON.stringify(user)));
        this.$loggedSubject.next(true);
        this.getPermissions();
    }

    public isLoggedIn() {
        let logged = moment().isBefore(this.getExpiration());
        return logged;
    }

    public getExpiration() {
        const expiration = sessionStorage.getItem("expires_at");
        const expiresAt = JSON.parse(expiration);
        return moment(expiresAt);
    }

    public logout(): void {
        sessionStorage.clear()
        this.$loggedSubject.next(false);
        this.router.navigate(['/']);
    }

    public getToken(): string {
        const token = sessionStorage.getItem("id_token");
        return token;
    }

    public getDataUser(): User {
        const user = JSON.parse(atob(sessionStorage.getItem("user")));
        return user;
    }

    public refreshToken() {
        const params: URLSearchParams = new URLSearchParams();
        params.set('grant_type', 'refresh_token');
        params.set('refresh_token', this.getRefreshToken());
        params.set('client_id', environment.clientId);
        params.set('client_secret', environment.clientSecret);
        params.set('scope', '');
        return this.SendAuthentication(params);
    }

    private getRefreshToken() {
        return sessionStorage.getItem('refresh_token');
    }

    public isPermitted(rota: string, child: string): boolean {
        if(!this.isLoggedIn()) {
			this.router.navigate(['/inicial/login']);
        }

        if(this.getDataUser().role == 'Sistema'){
            return true;
        }
        
        const route = this.getDataPermissions().find( element => element.route == rota );
        if( route == undefined || route == null ){
            return false;
        } else {
            return route.children.findIndex( item => item == child ) > -1;
        }
    }

    public getPermissions() {
        return this.http.get<any[]>('assets/config/permissions.json?v=' + environment.version).subscribe(
            response => {
                const role  = this.getDataUser().role;
                let userPermissions: any = [];
                response.forEach(element => {
                    if(element.roles.findIndex( item => item == role ) > -1){
                        let elementTemp : any = {};
                        elementTemp.children = [];
                        elementTemp.route = element.route
                        element.children.forEach(sub=> {
                            if(sub.roles.findIndex( item => item == role ) > -1){
                            elementTemp.children.push(sub.route);
                            }
                        });
                        userPermissions.push(elementTemp);
                    }
                });
                sessionStorage.setItem('permissions', btoa(JSON.stringify(userPermissions)));
            },
            () => {
                sessionStorage.setItem('permissions', btoa(JSON.stringify([])));
            }
        );
    }

    public getDataPermissions(): any[] {
        const permissions = JSON.parse(atob(sessionStorage.getItem("permissions")));
        return permissions;
    }

    public getMenu()  {
       return this.http.get<[]>('assets/config/menu.json?v=' + environment.version);
    }

    public hasPermission(permissions: string[]): boolean {
        const role = this.getDataUser().role;
        if( role == PermissionTypes.SISTEMA){
            return true;
        }
        return permissions.indexOf(role) > -1;
    }
}