
export class BrValidator {

	public static cpf(cpf: any): boolean {
		//retira caracteres especiais
		if (cpf != undefined || cpf != null) {
			cpf = cpf.replace(/[^a-z0-9]/gi, "");
			if (cpf.length == 11) {
				//verificar cpfs comuns
				let cpfsComuns = ["00000000000", "11111111111", "22222222222", "33333333333", "44444444444", "55555555555", "66666666666", "77777777777", "88888888888", "99999999999"];
				if (cpfsComuns.find(item => item == cpf) != undefined) {
					return false;
				}
				//verificadores
				let mult = 0;
				let cont = 0;
				//guardar digitos
				const dig1 = cpf.charAt(9);
				const dig2 = cpf.charAt(10);
				//multiplicar de 10 a 2
				for (let i = 10; i >= 2; i--) {
					mult += (cpf.charAt(cont) * i);
					cont++;
				}
				//pegar resto da divisão entre:
				mult = (mult * 10) % 11;
				//ser for maior ou igual a 10 o número recebe '0'
				if (mult >= 10) {
					mult = 0;
				}
				//verficiar se primeiro dígito está correto
				if (mult == dig1) {
					//repetir o passo todo agora com os 10 primeiros números incluindo o primeiro digito verificador
					mult = 0;
					cont = 0;
					//multiplicar de 10 a 2
					for (let i = 11; i >= 2; i--) {
						mult += (cpf.charAt(cont) * i);
						cont++;
					}
					//pegar resto da divisão entre:
					mult = (mult * 10) % 11;
					//ser for maior ou igual a 10 o número recebe '0'
					if (mult >= 10) {
						mult = 0;
					}
					//verficiar se segundo dígito está correto
					if (mult == dig2) {
						return true;
					}
				} else {
					return false;
				}
			}
		}

		return false;
	}

	public static cnpj(cnpj: any): boolean {
		if (cnpj != undefined || cnpj != null) {
			//retira caracteres especiais
			cnpj = cnpj.replace(/[^a-z0-9]/gi, "");
			if (cnpj.length == 14) {
				//verificar cnpjs comuns
				let cnpjsComuns = ["00000000000000", "11111111111111", "22222222222222", "33333333333333", "44444444444444", "55555555555555", "66666666666666", "77777777777777", "88888888888888", "99999999999999"];
				if (cnpjsComuns.find(item => item == cnpj) != undefined) {
					return false;
				}
				//verificadores
				let size = cnpj.length - 2
				let num = cnpj.substring(0, size);
				let dig = cnpj.substring(size);
				let soma = 0;
				let posicao = size - 7;
				//multiplicar casas
				for (let i = size; i >= 1; i--) {
					soma += num.charAt(size - i) * posicao--;
					if (posicao < 2) {
						posicao = 9;
					}
				}
				//pegar resto da divisão entre:
				let result = soma % 11 < 2 ? 0 : 11 - soma % 11;
				if (result != dig.charAt(0)) {
					return false;
				}
				size = size + 1;
				num = cnpj.substring(0, size);
				soma = 0;
				posicao = size - 7;
				for (let i = size; i >= 1; i--) {
					soma += num.charAt(size - i) * posicao--;
					if (posicao < 2) {
						posicao = 9;
					}
				}
				result = soma % 11 < 2 ? 0 : 11 - soma % 11;
				if (result != dig.charAt(1)) {
					return false;
				}
			} else {
				return false;
			}
		}

		return true;
	}

	public static conta(agencia: string, _conta: string, _digito: string): boolean {
		let digito = _digito;
		let conta = _conta;

		if(_digito == null || _digito == undefined){
			digito = _conta.slice(-1);
			conta = _conta.toString().substring(0,(_conta.length - 1));
		} 
		
		//valida caracteres de agência e conta
		if (!agencia || agencia.length != 4 || !conta || conta.length < 4 || conta.length > 11 || !digito) return false;
		//fator de validação Santander
		const fator = [9, 7, 3, 1, 0, 0, 0, 9, 7, 1, 3, 1, 9, 7, 3];
		const numeroAgencia = ("00000000000" + agencia).slice(-4);
		const numeroConta = ("00000000000" + conta).slice(-11);
		const agenciaConta = numeroAgencia + numeroConta;
		//aplicando o fator
		const multi = fator.map((element, index) => {
			const item = Number(element) * Number(agenciaConta[index]);
			return item % 10;
		});
		//soma dos resultados
		const total = multi.reduce((prev, element) => prev + element);
		const unidade = total % 10;
		let verificador = 10 - unidade;
		verificador = verificador % 10;
		
		if (verificador === Number(digito)) return true;

		return false;
	}

	public static agencia(valor: string): boolean {
		const er = /^[0-9]+$/;
		const isNumber =  (er.test(valor));
		if(valor.length == 4 && isNumber) return true;
		return false;
	}

	public static contaDigitos(valor: string): boolean {
		const er = /^[0-9]+$/;
  		const isNumber =  (er.test(valor));
		if((valor.length > 5 && valor.length < 15) && isNumber) return true;
		return false;
	}

	public static etiqueta(valor: string): boolean {
		const er = /^[0-9]+$/;
  		const isNumber =  (er.test(valor));
		if(valor.length == 9 && isNumber) return true;
		return false;
	}

	public static validadorDocumentoAtividade(documento: string, validacaoDocumento: number): boolean {
		if(validacaoDocumento == 1) return (this.cpf(documento) || this.cnpj(documento));
		if(validacaoDocumento == 2) return (documento =='' || documento == null || documento == undefined);
		if(validacaoDocumento == 3 || validacaoDocumento == 0) return (documento =='' || documento == null? true : this.cpf(documento) || this.cnpj(documento));
		return false;
	}

	public static validadorCpfAtividade(cpf: string, validacaoDocumento: number): boolean {
		if(validacaoDocumento == 1) return this.cpf(cpf);
		if(validacaoDocumento == 2) return (cpf =='' || cpf == null || cpf == undefined);
		if(validacaoDocumento == 3 || validacaoDocumento == 0) return (cpf =='' || cpf == null? true : this.cpf(cpf));
		return false;
	}

	public static validadorCnpjAtividade(cnpj: string, validacaoDocumento: number): boolean {
		if(validacaoDocumento == 1) return this.cnpj(cnpj);
		if(validacaoDocumento == 2) return (cnpj =='' || cnpj == null || cnpj == undefined);
		if(validacaoDocumento == 3 || validacaoDocumento == 0) return (cnpj =='' || cnpj == null? true : this.cnpj(cnpj));
		return false;
	}
}



