import { Component, OnInit, ViewChild, Input, Output, EventEmitter } from '@angular/core';
import { trigger, state, style, transition, animate } from '@angular/animations';
//import { ServiceCenterBaixaService } from '../services/service-center-baixa.service';
//import { ManagerialView, OperationManagerialView } from 'src/app/models/managerial-view.interface';
import { MatTableDataSource, MatPaginator, MatSnackBar, MatDialog, MatDialogRef, MatSelectChange } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
//import { PageConfig } from 'src/app/models/page-config';
import { StatusAnalyse } from 'src/app/models/status-analyse.interface';
import { PostAnalyse, OpenTicket, Analyse } from 'src/app/models/post-analyse.class';
import { Template, RejectionReasons } from 'src/app/models/template.model';
import { Atividade } from 'src/app/models/atividade.interface';
import { FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { BrValidator } from 'src/app/shared/validators/br-validators';
import { FormValidator } from 'src/app/shared/validators/form-validators';
import { ServiceCenterAnalise, Esteira, Carga, DataResult, modificada, CamposModificados, Conta } from 'src/app/models/service-center-analise.interface';
import { element } from 'protractor';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { GalleryItem } from 'src/app/models/gallery-item.interface';
import { DialogServiceCenterBaixaComponent } from './dialog-service-center-baixa/dialog-service-center-baixa.component';
import { ServiceCenterBaixaService } from 'src/app/modules/operation/services/service-center-baixa.service';
import { DialogHistoricokitComponent } from 'src/app/modules/operation/shared/dialog-historico-kit/dialog-historico-kit.component';


@Component({
	selector: 'app-service-center-baixa',
	templateUrl: './service-center-baixa.component.html',
	styleUrls: ['./service-center-baixa.component.scss'],
	animations: [
		trigger('detailExpand', [
			state('collapsed', style({ height: '0px', minHeight: '0' })),
			state('expanded', style({ height: '*' })),
			transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
		]),
	],
})

export class ServiceCenterBaixaComponent implements OnInit {

	@Input() template: Template;
	@Input() content: any;
	@Input() esteiraId: string;
	@Input() enableReview: boolean;
	@Input() atividade: Atividade;
	@Input() serviceCenterAnalise: ServiceCenterAnalise[] = [];
	@Output() submitTemplate: EventEmitter<any> = new EventEmitter<any>();

	public activities: number[] = [];
	public loading: boolean = false;
	public dataSource = new MatTableDataSource<ServiceCenterAnalise>();
	public columnsToDisplay = ['agencia', 'conta', 'documento', 'decisao', 'acoes', 'deletar'];
	public expandedElement: any;
	public showErrorMessage: boolean = false;
	public filter: string = null;
	public position: number = 0;
	public formHeader: FormGroup;
	private dialogHistoricoKitRef: MatDialogRef<DialogHistoricokitComponent, any>;
	public decisao: any[] = [];
	public edit: boolean = false;
	public finishContingency: boolean = false;
	public contingencyReasons: number[] = [];

	constructor(
		private serviceCenterBaixaService: ServiceCenterBaixaService,
		private formbuilder: FormBuilder,
		private matSnackBar: MatSnackBar,
		public dialog: MatDialog
	) { }

	ngOnInit() {
		this.template.actions.forEach(element=>{
			this.activities.push(element.value);
		})

		this.formHeader = this.formbuilder.group({
			agencia: [''],
			contaCorrente: [''],
			documento: ['', [this.validaDocumento]],
		});

		if(this.atividade.id == 11010200){
			this.decisao = [
				{ title: 'Procedente', value: 'Procedente' },
				{ title: 'Improcedente', value: 'Improcedente' }
			];
		} else {
			this.decisao = [
				{ title: 'Atendido', value: 'atendido' },
				{ title: 'Não Atendido', value: 'nao_atendido' }
			];
		}

		if(this.serviceCenterAnalise.length > 0) {
			this.edit = true;
			this.setTableDataPosition();
		}
	}

	public activeContingency(){
		this.finishContingency = !this.finishContingency;
	}

	public addConta() {
		if (this.formHeader.valid) {
			this.loading = true;
			const form = this.formHeader.value;
			this.serviceCenterBaixaService.getInformacaoConta(form.agencia, form.contaCorrente, form.documento).subscribe(
				response => {
					if (response.data.esteiras.length > 0 || response.data.cargas.length > 0) {
						let esteiras: Esteira[] = response.data.esteiras;
						esteiras.map(esteira => {
							esteira.camposModificados = { documento: false, contaCorrente: false, atividade: false }
						});
						let cargas: Carga[] = response.data.cargas;
						cargas.map(carga => {
							carga.camposModificados = { documento: false, contaCorrente: false, atividade: false }
						});
						const conta: ServiceCenterAnalise = {
							agencia: form.agencia,
							contaCorrente: form.contaCorrente,
							documento: form.documento,
							esteiras: esteiras,
							cargas: cargas
						};
						this.serviceCenterAnalise.unshift(conta);
						this.setTableDataPosition();
						this.expand(0);
						this.formHeader.get("agencia").setValue("");
						this.formHeader.get("contaCorrente").setValue("");
						this.formHeader.get("documento").setValue("");
					} else {
						SnackbarComponent.showAlert(this.matSnackBar, "Não há Esteira ou Carga para essa Conta");
					}
					this.loading = false;
				},
				error => {
					SnackbarComponent.showError(this.matSnackBar, error);
					this.loading = false;
				}
			);

		}
	}

	private setTableDataPosition() {
		this.serviceCenterAnalise.map((element, index) => element.position = index);
		this.dataSource.data = this.serviceCenterAnalise;
	}

	public delete(position: number) {
		this.serviceCenterAnalise = this.serviceCenterAnalise.filter(element => element.position != position);
		this.setTableDataPosition();
	}

	public expand(position: any) {
		if (this.expandedElement == this.dataSource.data[position]) {
			this.expandedElement = null;
			this.position = null;
		} else {
			this.expandedElement = this.dataSource.data[position];
			this.position = position;
		}
	}

	public openImages(documento: string, galleries: GalleryItem[]): void {
		const displayImages = {
			document: documento,
			images: galleries
		}
		localStorage.setItem('displayImages', JSON.stringify(displayImages));
		window.open('/#/operacao/exibir-imagens');
	}

	public editEsteira(serviceCenterAnalise: ServiceCenterAnalise) {

	}

	public openModal(element: any, tipo: string): void {
		const dialogRef = this.dialog.open(DialogServiceCenterBaixaComponent, {
			width: '80%',
			maxHeight: '80%',
			height: '90%',
			data: { element: element, tipo: tipo }
		});
		dialogRef.afterClosed().subscribe(result => {
			if (result == undefined) {
				SnackbarComponent.showAlert(this.matSnackBar, "Cancelada pelo usuário!");
			} else {
				if (tipo == "esteiras") {

					if (element.documento != result.documento) {
						element.documento = result.documento;
						element.camposModificados.documento = true;
					}

					if (element.contaCorrente != result.contaCorrente) {
						element.contaCorrente = result.contaCorrente;
						element.camposModificados.contaCorrente = true;
					}

					if (element.atividade.id != result.atividade.id) {
						element.atividade = result.atividade;
						element.camposModificados.atividade = true;
						this.loading = true;
						this.serviceCenterBaixaService.getLocalizaCarga(element.agencia, element.contaCorrente, element.atividade.id).subscribe(
							response => {							
								this.loading = false;
							},
							error => {
								this.loading = false;
								SnackbarComponent.showError(this.matSnackBar, error);
							}
						);
						
					}

				}
			}
		});
	}

	private validationsToSubmit(statusId: number): boolean {
		if (statusId === 8 && !this.contingencyReasons) {
			SnackbarComponent.showError(this.matSnackBar, false, "Você precisa escolher um motivo para a contingência");
			return false;
		} 
		return true;
	}


	public finalizar(statusId: number) {
		if (statusId != 8 && this.serviceCenterAnalise.findIndex(element => element.decisao == undefined || element.decisao == '') >= 0) {
			SnackbarComponent.showError(this.matSnackBar, false, "Atualize todas Decisões");
		} else {

			if (!this.validationsToSubmit(statusId)) {
				return false;
			}
			

			let contas: Conta[] = [];
			this.serviceCenterAnalise.forEach(element => {

				//conta análise
				const conta: Conta = {
					agencia: element.agencia,
					contaCorrente: element.contaCorrente,
					documento: element.documento,
					descisao: element.decisao
				}

				//esteiras modificadas
				let modificadas: modificada[] = [];
				element.esteiras.forEach(esteira => {
					if (esteira.camposModificados.documento == true || esteira.camposModificados.contaCorrente == true || esteira.camposModificados.atividade == true) {
						let modificada: modificada = {
							esteiraId: esteira.id,
							cargaId: esteira.cargaId,
							atividadeId: esteira.atividade.id,
							documento: esteira.documento,
							contaCorrente: esteira.contaCorrente
						}
						modificadas.push(modificada);
					}
				});
				conta.esteirasModificadas = modificadas;

				//cargas modificadas
				modificadas = [];			
				conta.cargasModificadas = modificadas;
				contas.push(conta);
			});

			const dataResult: DataResult = {
				im: this.content.incidentID,
				produto: this.content.produto,
				nomeAgencia: this.content.rede,
				categoria: this.content.category,
				subCategoria: this.content.subCategory,
				descricao: this.content.description,
				listaContas: contas
			}
			
			let body: Analyse = new Analyse();
			body.data = dataResult;
			body.entrarPausa = false,
			body.tipoPausaId = 0,
			body.finalizarExpediente = false,
			body.receberProximo = false,
			body.templateVersaoId = this.template.templateVersaoId;
			body.statusId = statusId;
			body.atividadeAcao = this.activities;
			body.motivosContingencia = [...this.contingencyReasons];
			this.submitTemplate.emit(body);
		}
	}
	
	public setReasonsToContingency(event: MatSelectChange): void {
		this.contingencyReasons = [event.value];
	}

	public atualizar() {
		if(this.serviceCenterAnalise.findIndex(element => element.visualizacao == true) <= 0) {
			SnackbarComponent.showError(this.matSnackBar, false, "Adicione um novo item");
		} else if (this.serviceCenterAnalise.findIndex(element => element.decisao == undefined || element.decisao == '') >= 0) {
			SnackbarComponent.showError(this.matSnackBar, false, "Atualize todas Decisões");
		} else {
			this.serviceCenterAnalise = this.serviceCenterAnalise.filter(element => element.visualizacao != true);
			let contas: Conta[] = [];
			this.serviceCenterAnalise.forEach(element => {

				//conta análise
				const conta: Conta = {
					agencia: element.agencia,
					contaCorrente: element.contaCorrente,
					documento: element.documento,
					descisao: element.decisao
				}

				//esteiras modificadas
				let modificadas: modificada[] = [];
				element.esteiras.forEach(esteira => {
					if (esteira.camposModificados.documento == true || esteira.camposModificados.contaCorrente == true || esteira.camposModificados.atividade == true) {
						let modificada: modificada = {
							esteiraId: esteira.id,
							cargaId: esteira.cargaId,
							atividadeId: esteira.atividade.id,
							documento: esteira.documento,
							contaCorrente: esteira.contaCorrente
						}
						modificadas.push(modificada);
					}
				});
				conta.esteirasModificadas = modificadas;

				//cargas modificadas
				modificadas = [];				
				conta.cargasModificadas = modificadas;
				contas.push(conta);
			});

			const dataResult: DataResult = {
				im: this.content.incidentID,
				produto: this.content.produto,
				nomeAgencia: this.content.rede,
				categoria: this.content.category,
				subCategoria: this.content.subCategory,
				descricao: this.content.description,
				listaContas: contas
			}
			
			let body: Analyse = new Analyse();
			body.data = dataResult;
			body.entrarPausa = false,
			body.tipoPausaId = 0,
			body.finalizarExpediente = false,
			body.receberProximo = false,
			body.templateVersaoId = this.template.templateVersaoId;
			body.statusId = 6;
			body.atividadeAcao = this.activities;
			this.submitTemplate.emit(body);
		}
	}

	public contingencia() {
		//regra contingência..
	}
	
	public openKitHistory(_element: any): void {
		this.dialogHistoricoKitRef = this.dialog.open(DialogHistoricokitComponent, {
			width: '80%',
			height: '80%',
			disableClose: false,
			data: {
				esteiraId : _element.id,
				agencia : _element.agencia,
				documento : _element.documento,
				contaCorrente : _element.contaCorrente
			}
		});
	}
	
	private validaDocumento(abstractControl: AbstractControl): any {
		const documento = abstractControl.value;
		if(documento.length == 0) return null;
		if (BrValidator.cpf(documento) || BrValidator.cnpj(documento)) {
			return null;
		}
		return { isError: true };
	}

}
