import { Component, OnInit, Inject, AfterViewInit, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Field, ColumnsTable } from 'src/app/models/template.model';

@Component({
	selector: 'app-dialog-add-column',
	templateUrl: './dialog-add-column.component.html',
	styleUrls: ['./dialog-add-column.component.scss']
})
export class DialogAddColumnComponent implements OnInit, AfterViewInit {

	@ViewChild('addFieldFormModal', {static : true}) addFieldFormComponent: any;
	public form: FormGroup;
	public formFields: Field[] = [];
	public editingField: boolean;
	public isValid: boolean;
	private fieldInFocus: string;

	constructor(
		private formBuilder: FormBuilder,
		public dialogRef: MatDialogRef<DialogAddColumnComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
	) { }
	
	ngOnInit() {
		this.form = this.formBuilder.group({
			label : ['', Validators.required],
		});
	
		this.formFields = this.data.formFields;
		this.editingField = this.data.editingField;
	}

	ngAfterViewInit() {
		setTimeout(() => {
			if (this.editingField) {
				this.addFieldFormComponent.editField(this.data.columnsTable.field);
				this.form.patchValue({
					label: this.data.columnsTable.label
				})
				this.fieldInFocus = this.data.columnsTable.name;
			}
		}, 0);

		this.form.valueChanges.subscribe(
			() => {
				this.isValid = this.form.valid;
			}
		)
	}

	public addField(field: Field): void {
		const column: ColumnsTable = new ColumnsTable();
		column.name = this.editingField ? this.fieldInFocus : null;
		column.label = this.form.value.label;
		column.field = field;
		this.dialogRef.close(column);
	}

	public cancelEditionField() {
		this.dialogRef.close();
	}
}
