import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatTableDataSource, MatSnackBar } from '@angular/material';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { ServiceCenterAnalise } from 'src/app/models/service-center-analise.interface';
import { FormValidator } from 'src/app/shared/validators/form-validators';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { OperationService } from 'src/app/services/operation.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { BrValidator } from 'src/app/shared/validators/br-validators';


@Component({
	selector: 'app-dialog-service-center-baixa.component',
	templateUrl: './dialog-service-center-baixa.component.html',
	styleUrls: ['./dialog-service-center-baixa.component.scss']
})
export class DialogServiceCenterBaixaComponent implements OnInit {

	public loading: boolean = false;
	public form: FormGroup;
	public formDoc: FormGroup;
	public element: any;
	public operations: Operacao[] = [];
	public activities: Atividade[] = [];
	
	constructor(
		public dialogRef: MatDialogRef<DialogServiceCenterBaixaComponent>,
		@Inject(MAT_DIALOG_DATA) public modalData: any,
		private formBuilder: FormBuilder,
		private matSnackBar: MatSnackBar,
		private operationService: OperationService,
	) { }

	ngOnInit() {
		this.element = this.modalData.element;
		
		this.form = this.formBuilder.group({
			documento: [this.element.documento],
			contaCorrente: [this.element.contaCorrente,[Validators.required]],
			atividade: ['', [Validators.required]],
			operacaoId: ['', Validators.required],
		});

		//this.activities.push(this.element.atividade);
		this.activities[0] = this.element.atividade;
		this.formDoc = this.formBuilder.group({
			documento: ['', [Validators.required, FormValidator.documento]]
		});

		this.operacoes();
	}

	private operacoes() {
		this.loading = true;
		this.operationService.getOperations(true).subscribe(
			response => {
				this.operations = [...response.data];
				//produto config
				this.operations.forEach(element=> {
					element.atividades.forEach(item => {
						if(item.id == this.element.atividade.id) {
							this.form.get("operacaoId").setValue(element.operacaoId);
							this.activities = element.atividades;
							const atividade = this.activities.find(atv=>atv.id == this.element.atividade.id);
							this.form.get("atividade").setValue(atividade);
							this.form.get('documento').setValidators([this.documento]);
						}
					});
					
				});
				this.loading = false;
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter a lista de Operações e Atividades. Se o erro persistir, entre em contato com o suporte.");
				this.loading = false;
			}
		);
	}

	public setOperation(): void {
		const operation: Operacao = this.operations.find(el => el.operacaoId === this.form.value.operacaoId);
		this.activities = [...operation.atividades];
	}

	public salvar() {
		if (this.form.valid) {
			// if (this.modalData.tipo == 'esteiras') {
			// 	this.postEsteira();
			// } else if (this.modalData.tipo == 'cargas') {
			// 	this.postCarga();
			// }
			this.update();
		}
	}

	public setAtividade(){
		this.form.get('documento').updateValueAndValidity();
	}


	private update() {
		const form = this.form.value;
		this.dialogRef.close(form);
	}
	
	public fecharModal(status?: string) {
		this.dialogRef.close(status);
	}

	private  documento(abstractControl: AbstractControl): any {
		const valor = abstractControl.value;
		const validacaoDocumento = abstractControl.parent.value.atividade.validacaoDocumento;
		
		if (BrValidator.validadorDocumentoAtividade(valor,validacaoDocumento)) {
			return null;
		}
		return { isError: true };
	}

}
