import { Component, OnInit, OnDestroy } from '@angular/core';
import { Field } from 'src/app/models/template.model';
import { FormGroup } from '@angular/forms';
import { EsteiraService } from 'src/app/modules/operation/services/esteira.service';
import { MessengerService } from 'src/app/services/messenger.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
	selector: 'app-upload',
	templateUrl: './upload.component.html',
	styleUrls: ['./upload.component.scss']
})
export class UploadComponent implements OnInit, OnDestroy {

	public field: Field;
	public group: FormGroup;
	public data = [];
	public progressBar: boolean = false;
	private ngUnsubscribe = new Subject();

	constructor(
		private esteiraService: EsteiraService,
		private messengerService: MessengerService
	) { }

	ngOnInit() {
		if(this.field.value != undefined) this.data = this.field.value;
		this.field.visible = Boolean(this.field.visible.toString() === "true");
		this.field.disabled = Boolean(this.field.disabled.toString() === "true");
		this.field.required = Boolean(this.field.required.toString() === "true");
		this.messengerService.getMessage().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
			response => {				
				this.data = response.data;					
			}
		);		
	}

	handleFileInput(files: FileList) {
		const formData: FormData = new FormData();
		for (let cont = 0; cont < files.length; cont++) {
			const fileToUpload: File = files.item(cont);
			formData.append('files', fileToUpload, fileToUpload.name);
		}
		this.progressBar = true;
		this.esteiraService.uploadConvert(formData, "png").subscribe(
			response => {
				this.progressBar = false;
				 response.data.forEach(element => {
					this.data.push(element);
				 });
				 this.messengerService.dispatchAction(this.field.type, this.data);
				 (<HTMLInputElement>document.getElementById('file')).value = "";
			},
			() => {
				this.progressBar = false;
				(<HTMLInputElement>document.getElementById('file')).value = "";
			}
		);
	}

	public deletar(image: string): void {
		this.data = this.data.filter(img => img != image);
		this.messengerService.dispatchAction(this.field.name, this.data);
	}

	ngOnDestroy() {
		this.ngUnsubscribe.next();
		this.ngUnsubscribe.complete();
	}
}
