import { Component, OnInit } from '@angular/core';
import { Field } from 'src/app/models/template.model';
import { FormGroup } from '@angular/forms';
import { GeneralInformationsService } from 'src/app/services/general-informations.service';
import { MessengerService } from 'src/app/services/messenger.service';
import { MatSnackBar } from '@angular/material';
import { SnackbarComponent } from '../../snackbar/snackbar.component';
import { PostIntegratedSearch } from 'src/app/models/data-source-integrated-search.interface';

@Component({
	selector: 'app-integrated-search',
	templateUrl: './integrated-search.component.html',
	styleUrls: ['./integrated-search.component.scss']
})
export class IntegratedSearchComponent implements OnInit {

	public field: Field;
	public group: FormGroup;
	public loading: boolean;
	
	constructor(
		private generalInformationsService: GeneralInformationsService,
		private messengerService: MessengerService,
		private matSnackBar: MatSnackBar,
	) {
	}
	
	ngOnInit() {
		this.field.visible = this.field.visible.toString() === "true" ? true : false;
		this.field.disabled = this.field.disabled.toString() === "true" ? true : false;
		this.field.required = this.field.required.toString() === "true" ? true : false;

		if (this.field.disabled) {
			this.group.get(this.field.name).disable();
		}

		if (this.field.mask === "numbers" && this.field.maxLength !== "") {
			let mask = "";
			
			while(mask.length < Number(this.field.maxLength)) {
				mask += "9";
			}
			this.field.mask = mask;
		} else if (this.field.mask === "numbers" && this.field.maxLength === "") {
			this.field.mask = "99999999999999";
		}
	}

	public search(): void {
		const data: string = this.group.get(this.field.name).value;
		if (data) {
			this.loading = true;
			// const body: PostIntegratedSearch = new PostIntegratedSearch();
			// body.tipo = this.field.integratedSearch.type;
			// body.campoCriterio = this.field.integratedSearch.criteria;
			// body.valorCriterio = data;
			let body: any = {};
			body.tabela = this.field.integratedSearch.type;;
			body.campo =  this.field.integratedSearch.criteria;
			body.criterio = data;

			this.generalInformationsService.getDateIntegratedSearch(body).subscribe(
				response => {
					this.messengerService.dispatchAction(this.field.name, response);
					if (response) {
						SnackbarComponent.showSuccess(this.matSnackBar, 'Pesquisa realizada com sucesso');
					} else {
						SnackbarComponent.showError(this.matSnackBar, null, 'A pesquisa não encontrou nada com o valor digitado');
					}
					this.loading = false;
				},
				() => {
					SnackbarComponent.showError(this.matSnackBar, null, 'A pesquisa falhou');
					this.loading = false;
				}
			)
		}
	}
}