import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Field } from 'src/app/models/template.model';
import * as moment from 'moment';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
	selector: 'app-input',
	templateUrl: './input.component.html',
	styleUrls: ['./input.component.scss']
})
export class InputComponent implements OnInit {
	
	public field: Field;
	public group: FormGroup;
	
	constructor(
		public utilsService: UtilsService
	) {}
	
	ngOnInit() {
		this.field.visible = this.field.visible.toString() === "true";
		this.field.disabled = this.field.disabled.toString() === "true";
		this.field.required = this.field.required.toString() === "true";

		if (this.field.disabled && !this.field.readOnly) {
			this.group.get(this.field.name).disable();
		}

		if (this.field.required && !this.field.label.includes("(obrigatório)") && this.field.label !== '') {
			this.field.label = this.field.label +" (obrigatório)";
		}

		if (this.field.mask === "numbers" && this.field.maxLength !== "") {
			let mask = "";
			
			while(mask.length < Number(this.field.maxLength)) {
				mask += "9";
			}
			this.field.mask = mask;
		} else if (this.field.mask === "numbers" && this.field.maxLength === "") {
			this.field.mask = "99999999999999";
		}

		if (this.field.value === '' && this.field.currentDate && this.field.currentDate.toString() === "true") {
			this.field.value = moment().format('DD/MM/YYYY HH:mm');
		}
	}

	public changeValue(event: any): void {
		if (this.field.mask === 'separator.2' && event.target.value.indexOf(',') < 0) {
			this.group.get(this.field.name).setErrors({
				required: true
			})
		}
	}
}