import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Template, RejectionReasons } from 'src/app/models/template.model';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatCheckboxChange, MatSelectChange, MatDialog, MatSnackBar } from '@angular/material';
import { DialogComponent } from '../dialog/dialog.component';
import { Dialog } from 'src/app/models/dialog.model';
import { PostAnalyse, OpenTicket, Analyse } from 'src/app/models/post-analyse.class';
import { TemplateTypes } from 'src/app/models/template-types.enum';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { GalleryItem } from 'src/app/models/gallery-item.interface';
import { DialogListHistoryComponent } from 'src/app/modules/operation/analyze/dialog-list-history/dialog-list-history.component';
import { StatusAnalyse } from 'src/app/models/status-analyse.interface';
import { DialogRequestReviewComponent } from './dialog-request-review/dialog-request-review.component';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { EsteiraService } from 'src/app/modules/operation/services/esteira.service';
import { AlertaAtividade, Atividade } from 'src/app/models/alerta-atividade.interface';
import { DialogTrocarEsteiraComponent } from './dialog-trocar-esteira/dialog-trocar-esteira.component';


@Component({
	selector: 'app-template',
	templateUrl: './template.component.html',
	styleUrls: ['./template.component.scss']
})
export class TemplateComponent implements OnInit {

	@Input() template: Template;
	@Input() content: Object;
	@Input() type: TemplateTypes;
	@Input() galleries: GalleryItem[];
	@Input() status: StatusAnalyse;
	@Input() esteiraId: string;
	@Input() enableReview: boolean;
	@Input() alertaAtividades: AlertaAtividade[];
	@Input() alertaDocumentos: AlertaAtividade[];
	@Input() atividade: Atividade;
	@Input() anexos: any[];
	@Output() submitTemplate: EventEmitter<any> = new EventEmitter<any>();
	public loading: boolean;
	public activities: number[] = [];
	public formHeader: FormGroup;
	public rejectionReasons: number[] = [];
	public contingencyReasons: number[] = [];
	public showRejectionReasons: boolean = false;
	public showContingencyReasons: boolean = false;
	public templateV1: boolean;
	public templateId: number;

	constructor(
		private formbuilder: FormBuilder,
		public dialog: MatDialog,
		private esteiraService: EsteiraService,
		private matSnackBar: MatSnackBar
	) { }

	ngOnInit() {
		this.formHeader = this.formbuilder.group({
			documento: [''],
			agencia: [''],
			contaCorrente: [''],
			numeroProcesso: [''],
			numeroPasta: ['']
		});
		this.restart();		

		if (this.type === TemplateTypes.EDIT || this.type === TemplateTypes.VIEW) {
			if (this.status && this.status.id === 4) {
				this.showRejectionReasons = true;
				const rejectionReasons = this.template.rejectionReasons.filter(el => el.checked).map(el => el.value);
				this.rejectionReasons = [...rejectionReasons];
			}
		}

		if (this.template && this.template.decision && this.template.decision.buttons.length > 0) {
			this.templateV1 = false;
		} else {
			this.templateV1 = true;
		}

		if (this.alertaAtividades != undefined && this.alertaAtividades.length > 0) {
			this.openAlerta(this.alertaAtividades[0], 'Atividade');
		}

		if (this.alertaDocumentos != undefined && this.alertaDocumentos.length > 0) {
			if (this.type !== 2) {
				const alerta = this.alertaDocumentos.find(element => element.documento.replace(/[^\d]+/g, '') == this.template.header.docNumber);
				if (alerta != undefined) {
					this.openAlerta(alerta, 'Documento')
				}
			} else if (this.type == 2) {
				this.formHeader.get("documento").valueChanges.subscribe(
					response => {
						const alerta = this.alertaDocumentos.find(element => element.documento.replace(/[^\d]+/g, '') == response);
						if (alerta != undefined) {
							this.openAlerta(alerta, 'Documento')
						}
					}
				);
			}
		}
	}

	public submit(formValue: PostAnalyse): void {


		if (this.type !== TemplateTypes.OPEN_TICKET) {
			/** Teste para verificar a versão do template */
			if (this.template.decision && this.template.decision.buttons.length > 0) {
				this.templateV1 = false;
			} else {
				this.templateV1 = true;
			}

			this.activities = [...formValue.atividadeAcao];

			let body: Analyse = new Analyse();
			body.data = formValue.data;
			body.templateVersaoId = this.template.templateVersaoId;
			body.statusId = formValue.statusId;
			body = this.adjustments(body, formValue);
			body.anexo = formValue.anexo;
			if (body) {
				this.submitTemplate.emit(body);
				if (this.type !== TemplateTypes.PREVIEW && this.type !== TemplateTypes.EDIT) {
					this.restart();
				}
			} else {
				return;
			}
		}

		if (this.type === TemplateTypes.OPEN_TICKET) {
			if (this.formHeader.valid) {
				/** Teste para verificar a versão do template */
				if (this.template.decision && this.template.decision.buttons.length > 0) {
					this.templateV1 = false;
				} else {
					this.templateV1 = true;
				}

				let body: OpenTicket = new OpenTicket();
				body.data = formValue.data;
				body.statusId = formValue.statusId;
				body.documento = this.formHeader.value.documento;
				body.agencia = this.formHeader.value.agencia;
				body.contaCorrente = this.formHeader.value.contaCorrente;
				body.numeroProcesso = this.formHeader.value.numeroProcesso;
				body.numeroPasta = this.formHeader.value.numeroPasta;
				body = this.adjustments(body, formValue);
				body.anexo = formValue.anexo;
				if (body) {
					this.submitTemplate.emit(body);
					this.restart();
				} else {
					return;
				}
			}

		}
	}

	private restart(): void {
		this.activities = [];
		this.rejectionReasons = [];
		this.showRejectionReasons = false;
		this.showContingencyReasons = false;
		this.formHeader.reset();
	}

	private adjustments(body: PostAnalyse, formValue: PostAnalyse): any {
		/** Template 1.0 */
		if (this.templateV1) {
			if (this.validationsToSubmit(formValue.statusId)) {
				body.atividadeAcao = [...this.activities];
				body.motivosRejeicao = [...this.rejectionReasons];
				body.motivosContingencia = [...this.contingencyReasons];
				return body;
			}
		}

		/** Template 2.0 */
		if (!this.templateV1) {
			body.atividadeAcao = [...formValue.atividadeAcao];
			body.motivosRejeicao = [...formValue.motivosRejeicao];
			body.motivosContingencia = [...formValue.motivosContingencia];
			return body;
		}

		return null;
	}

	private validationsToSubmit(statusId: number): boolean {
		if (statusId === 4 && !this.showRejectionReasons) {
			this.showRejectionReasons = true;
			return false;
		}

		if (statusId === 8 && !this.showContingencyReasons) {
			this.showContingencyReasons = true;
			return false;
		}

		if (statusId === 4 && this.rejectionReasons.length < 1) {
			const dialog: Dialog = new Dialog();
			dialog.title = "Motivos da Reprovação";
			dialog.message = "Você precisa escolher um motivo para a reprovação";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
			return false;
		}

		if (statusId === 8 && !this.contingencyReasons) {
			const dialog: Dialog = new Dialog();
			dialog.title = "Motivo da Contingência";
			dialog.message = "Você precisa escolher um motivo para a contingência";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
			return false;
		}

		if (statusId !== 8 && this.activities.length < 1) {
			const dialog: Dialog = new Dialog();
			dialog.title = "Ações Realizadas na Atividade";
			dialog.message = "Você precisa realizar ao menos uma ação em cada análise.";
			dialog.type = DialogTypes.INFORMATION;
			this.showMessage(dialog);
			return false;
		}

		return true;
	}

	public setActivity(event: MatCheckboxChange, value: number): void {
		if (event.checked) {
			this.activities = [...this.activities, value];
		} else {
			this.activities = this.activities.filter(el => el !== value);
		}
	}

	public getReasonsToReject(): RejectionReasons[] | number[] {
		if (this.type === TemplateTypes.VIEW && this.status.id === 4) {
			return this.template.rejectionReasons.filter(el => el.checked);
		} else {
			return this.template.rejectionReasons.filter(el => el.value !== 9999);
		}
	}

	public getReasonsToRejectChecked(): number[] {
		return this.rejectionReasons;
	}

	public setReasonsToReject(event: MatSelectChange): void {
		this.rejectionReasons = [];
		this.rejectionReasons = [...event.value];
	}

	public setReasonsToContingency(event: MatSelectChange): void {
		this.contingencyReasons = [event.value];
	}

	public openImages(): void {
		const displayImages = {
			document: this.template.header.docNumber,
			images: this.galleries
		}
		localStorage.setItem('displayImages', JSON.stringify(displayImages));
		window.open('/#/operacao/exibir-imagens');
	}

	public openHistory(): void {
		this.dialog.open(DialogListHistoryComponent, {
			width: '1200px',
			disableClose: false,
			data: {
				agencia: this.template.header.bankBranch,
				documento: this.template.header.docNumber,
				contaCorrente: this.template.header.bankAccount
			}
		});
	}

	public requestReview(): void {

		const dialog = this.dialog.open(DialogRequestReviewComponent, {
			width: '600px',
		});

		dialog.afterClosed().subscribe(
			response => {
				if (response) {
					this.loading = true;
					this.esteiraService.requestReview(this.esteiraId, response).subscribe(
						() => {
							let body: Analyse = new Analyse();
							this.submitTemplate.emit(body);
							this.loading = false;
						},
						error => {
							this.loading = false;
							SnackbarComponent.showError(this.matSnackBar, null, error.error.message);
						}
					)
				}
			}
		);
	}

	public alterarEsteira(): void {

		const dialog = this.dialog.open(DialogTrocarEsteiraComponent, {
			width: '600px',
		});

		dialog.afterClosed().subscribe(
			response => {
				if (response) {
					const atividadeId = response;
					this.loading = true;
					this.esteiraService.changeEsteira(this.esteiraId, atividadeId, true).subscribe(
						() => {
							this.loading = false;
							location.reload();
						},
						error => {
							this.loading = false;
							SnackbarComponent.showError(this.matSnackBar, error);
						}
					)
				}
			}
		);
	}

	public showMessage(dialog: Dialog): void {
		this.dialog.open(DialogComponent, {
			width: '600px',
			data: dialog
		});
	}

	public openAlerta(alerta: AlertaAtividade, tipo: string) {
		const dialog: Dialog = new Dialog();
		dialog.title = "Alerta de " + tipo;
		dialog.message = alerta.mensagem;
		dialog.type = DialogTypes.INFORMATION;
		this.showMessage(dialog);
	}

}
