export class User {
    public welcomeName: string;
    constructor(
        public nome: string,
        public role: string,
        public email: string,
        public givenName: string,
        public surName: string
    ){
        if (this.givenName && this.givenName !== ''){
            this.welcomeName = this.givenName;
        }
        if (this.surName && this.surName !== ''){
            this.welcomeName += ` ${this.surName}` ;
        }
        if (!this.welcomeName){
            this.welcomeName = this.nome;
        }
    }
}