import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Field } from 'src/app/models/template.model';


@Component({
	selector: 'app-select',
	templateUrl: './select.component.html',
	styleUrls: ['./select.component.scss']
})
export class SelectComponent implements OnInit {
	
	public field: Field;
	public group: FormGroup;
	
	constructor() { }
	
	ngOnInit() {
		this.field.visible = Boolean(this.field.visible.toString() === "true");
		this.field.disabled = Boolean(this.field.disabled.toString() === "true");
		this.field.required = Boolean(this.field.required.toString() === "true");
		
		if (this.field.selectMultiple) {
			this.field.selectMultiple = Boolean(this.field.selectMultiple.toString() === "true");
		}

		if (this.field.disabled) {
			this.group.get(this.field.name).disable();
		}
	}
	
}
