import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
	name: 'nameStatusKits'
})
export class NameStatusKitsPipe implements PipeTransform {
	
	transform(value: number, args?: any): string {
		
		if (value === 1) {
			return "Aguardando Digitalização";
		} else if (value === 2) {
			return "Aguardando OCR";
		} else if (value === 3) {
			return "Triagem Manual";
		} else if (value === 4) {
			return "Aguardando Análise";
		} else if (value === 5) {
			return "Em Análise";
		} else if (value === 6) {
			return "Aguardando Devolução";
		} else if (value === 7) {
			return "Devolvida";
		} else if (value === 8) {
			return "Aguardando Arquivo";
		} else if (value === 9) {
			return "Enviada para Arquivo";
		} else if (value === 10) {
			return "Arquivado";
		} else {
			return null;
		}
	}
	
}
