import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Field } from 'src/app/models/template.model';

@Component({
	selector: 'app-checkbox',
	templateUrl: './checkbox.component.html',
	styleUrls: ['./checkbox.component.scss']
})
export class CheckboxComponent implements OnInit {
	
	public field: Field;
	public group: FormGroup;
	public checked: boolean;
	
	constructor() { }
	
	ngOnInit() {
		this.field.visible = Boolean(this.field.visible.toString() === "true");
		this.field.disabled = Boolean(this.field.disabled.toString() === "true");
		this.field.required = Boolean(this.field.required.toString() === "true");
		this.checked = Boolean(this.field.value.toString() === "true");
		
		if (this.field.disabled) {
			this.group.get(this.field.name).disable();
		}
	}
}
