import { Component, OnInit } from '@angular/core';
import { AutenticationService } from 'src/app/services/autentication.service';
import { Router } from '@angular/router';
import { environment } from '../../../../environments/environment';

@Component({
	selector: 'app-menu',
	templateUrl: './menu.component.html',
	styleUrls: ['./menu.component.scss']
})

export class MenuComponent implements OnInit {

	public dataMenu: any;
	public environment: any = environment;

	constructor(
		private autenticationService: AutenticationService,
		private router: Router
	) { }

	ngOnInit() {
		this.autenticationService.getMenu().subscribe(
			response => {
				this.permissionsMenu(response);
			},
			() => {
				this.router.navigate(['/inicial/home']);
			}
		);
	}

	public permissionsMenu(data: any) {
		if (this.autenticationService.getDataUser().role == 'Sistema') {
			this.dataMenu = data;
		}
		else {
			const permissions = this.autenticationService.getDataPermissions();
			//permission route
			const permissionData = data.filter(element => {
				const elementPermission = permissions.find(item => item.route == element.route);
				if (elementPermission != undefined) {
					return element;
				}
			});

			//permission child route
			let arr: any = [];
			permissionData.forEach(element => {
				let elementTemp: any = {};
				elementTemp.children = [];
				element.children.forEach(sub => {
					let itemTemp: any = {};
					permissions.forEach(item => {
						if (item.route == element.route) {
							if (item.children.find(rt => rt == sub.route) != undefined) {
								itemTemp.name = sub.name;
								itemTemp.id = sub.id;
								itemTemp.icon = sub.icon;
								itemTemp.route = sub.route;
								itemTemp.children = sub.children
							}
						}
					});
					if (itemTemp.route != undefined) {
						elementTemp.name = element.name;
						elementTemp.id = element.id;
						elementTemp.icon = element.icon;
						elementTemp.route = element.route;
						elementTemp.children.push(itemTemp);
					}
				});
				arr.push(elementTemp);
			});
			this.dataMenu = arr;
		}
	}

	public toggleMenu(menu: string) {
		const element = document.getElementById('sub_' + menu);
		const iconMenu = document.getElementById('icon_' + menu);
		if (element.className == 'sub-menu-close') {
			//subMenu
			element.classList.remove('sub-menu-close');
			element.classList.add('sub-menu-open');
			//icon
			iconMenu.classList.remove('icon_menu_close');
			iconMenu.classList.add('icon_menu_open');
		} else if (element.className == 'sub-menu-open') {
			//subMenu
			element.classList.remove('sub-menu-open');
			element.classList.add('sub-menu-close');
			//icon
			iconMenu.classList.remove('icon_menu_open');
			iconMenu.classList.add('icon_menu_close');
		}
	}

}
