import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Field } from 'src/app/models/template.model';
import { FormGroup } from '@angular/forms';

@Component({
	selector: 'app-comparation',
	templateUrl: './comparation.component.html',
	styleUrls: ['./comparation.component.scss']
})
export class ComparationComponent implements AfterViewInit {

	public field: Field;
	public group: FormGroup;
	public status: boolean;

	constructor() { }

	ngAfterViewInit() {
		this.group.valueChanges.subscribe(
			response => {
				
				const source: string = response[this.field.comparation.source];
				const operator: string = this.field.comparation.operator;
				const compareWith: string = this.field.comparation.compareWith;
				const targetValue: string = compareWith === 'custom' ? this.field.comparation.target : response[this.field.comparation.target];

				switch (operator) {
					case 'equal':
						this.status = source === targetValue;
						break;
					case 'notEqual':
						this.status = source !== targetValue;
						break;
					case 'greaterThan':
						this.status = source > targetValue;
						break;
					case 'lessThan':
						this.status = source < targetValue;
						break;
					default:
						this.status = undefined;
						break;
				}
			}
		)
	}

}
