import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { Field, Condition } from 'src/app/models/template.model';
import { FormGroup, FormBuilder, Validators, FormArray, AbstractControl } from '@angular/forms';
import { MessengerService } from 'src/app/services/messenger.service';
import { Dialog } from 'src/app/models/dialog.model';
import { DialogComponent } from '../../dialog/dialog.component';
import { MatDialog } from '@angular/material';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { DialogTypes } from 'src/app/models/dialog-types.enum';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
	selector: 'app-table',
	templateUrl: './table.component.html',
	styleUrls: ['./table.component.scss']
})
export class TableComponent implements OnInit, AfterViewInit, OnDestroy {

	public field: Field;
	public group: FormGroup;
	public dataTable: any[] = [];
	
	public content: Object[];
	
	public formTable: FormGroup;
	public formArrayTable: FormArray;

	private conditionsToVisible: boolean[] = [];
	private conditionsToEnable: boolean[] = [];
	private fieldsTable: Field[][] = [];
	public formValidations: boolean[] = []

	private ngUnsubscribe = new Subject();

	constructor(
		private fb: FormBuilder,
		private messengerService: MessengerService,
		public dialog: MatDialog,
		public utilsService: UtilsService
	) { }

	ngOnInit() {
		this.field.visible = Boolean(this.field.visible.toString() === "true" ? true : false);
		this.field.required = false;

		this.dataTable = [...this.field.value];
		this.content = this.field.value;
		
		this.formArrayTable = this.createFormArray();
		
		this.formTable = this.fb.group({
			registros : this.formArrayTable
		});

		this.group.get(this.field.name).setValue(this.formTable.value);

		this.field.columnsTable.map(el => {
			let arrayFieldsRegister: Field[] = [];
			this.dataTable.map(() => {
				const field = Object.assign({}, el.field);
				arrayFieldsRegister = [...arrayFieldsRegister, field];	
			})
			this.fieldsTable = [...this.fieldsTable, arrayFieldsRegister];
		});
	}

	ngAfterViewInit() {
		this.formTable.valueChanges.pipe(takeUntil(this.ngUnsubscribe)).subscribe(() => {
			this.formArrayTable.controls.map(formGroup => {
				this.fieldsTable.map(ArrayField => {
					ArrayField.map(field => {
						if (formGroup.get(field.name).value === '') {
							formGroup.get(field.name).setErrors({
								required: true
							})
						}
					})
				})
			})

			this.group.get(this.field.name).setValue(this.formTable.value);
		})

		this.messengerService.getMessage().pipe(takeUntil(this.ngUnsubscribe)).subscribe(
			response => {
				if (response.source === this.field.name && response.data === 'validTableToSubmit') {
					this.formValidations = []

					this.formArrayTable.controls.map(array => {
						this.formValidations = [...this.formValidations, array.valid];
					})
	
					if (this.formValidations.includes(false) && this.formValidations.length > 0) {
						const dialog: Dialog = new Dialog();
						dialog.title = "Problemas com os formulários da tabela!";
						dialog.message = "Você precisa corrigir/preencher todos as linhas destacadas em vermelho";
						dialog.type = DialogTypes.INFORMATION;
						this.showMessage(dialog);
					} else {
						this.messengerService.dispatchAction(this.field.name, 'tableValid');
					}
				}
			}
		);
	}

	ngOnDestroy() {
		this.ngUnsubscribe.next();
		this.ngUnsubscribe.complete();
	}

	public getTitleHead(): string[] {
		let th: string[] = [];
		this.field.columnsTable.map(el => {
			th = [...th, el.label];
		})
		return th;
	}

	public testToGetClass(index: number): boolean {
		if (this.formValidations.length > 0) {
			return this.formValidations[index];
		} else {
			return true;
		}
	}

	private createFormArray(): FormArray {
		
		let mainGroup: FormArray = new FormArray([]);
		this.dataTable.map( (el) => {
			const group: FormGroup = this.fb.group({});
			const elArray = Object.entries(el);
			
			this.field.columnsTable.map((item) => {
				let value: string = "";

				if (item.field.type === "button" && item.field.buttonType === "submit") return;
				if (item.field.type === "subtitle") return;

				elArray.map(elArrayItem => {
					if (item.field.dataSource === elArrayItem[0]) {
						value = elArrayItem[1].toString();
					}
				})
				
				const control = this.fb.control(value, ( item.field.required === true ? [Validators.required] : [] ));
				group.addControl(item.field.name, control);
			})
			mainGroup.push(group);
		});
		return mainGroup;
	}

	public getFields(index: number): Field[] {
		let fields: Field[] = [];
		const data = Object.entries(this.dataTable[index]);

		this.fieldsTable.map((arrayFieldsTable) => {
			data.map((dataTable) => {
				if (arrayFieldsTable[index].dataSource === dataTable[0]) {
					arrayFieldsTable[index].value = dataTable[1].toString();
				}
			});
			fields = [...fields, arrayFieldsTable[index]];
		});

		return fields;
	}

	public detectValueChange(event: any, index: number, field: Field): void {
		this.testAllConditions(field, event, index);
	}

	private testAllConditions(field: Field, response: any, index: number ): void {

		/** Testa condições de visibilidade */
		if (field.conditionsToVisible.length > 0) {

			this.conditionsToVisible = [];
			field.conditionsToVisible.map(condition => {
				this.testFieldsToVisible(response, condition);
			});

			if (this.conditionsToVisible.indexOf(false) < 0) {
				field.visible = true;
			} else {
				field.visible = false;
			}
		}

		/** Testa condições para os campos estarem habilitados */
		if (field.conditionsToEnable.length > 0) {
			
			this.conditionsToEnable = [];

			field.conditionsToEnable.map(condition => {
				this.testFieldsToEnable(response, condition)
			});

			this.enableField(field, index);
		}

		/** Definir valor de campo se opção for selecionada */
		if ((field.type === 'select' || field.type === 'radiobutton') && response[field.name] !== "") {

			field.options.map(option => {

				if (response[field.name] === option.value && option.conditionOption) {
					option.conditionOption.map(conditionOption => {

						let field2: Field = new Field();
						this.fieldsTable.map(fieldArray => {
							fieldArray.map((field, indexFieldArray ) => {
								if (indexFieldArray === index) {
									if (field.name === conditionOption.fieldName) {
										field2 = Object.assign({}, field);
									}
								}
							})
						});
						
						const formControl: AbstractControl = this.formArrayTable.controls[index].get(conditionOption.fieldName);

						if (formControl.value !== conditionOption.value) {

							if (conditionOption.value === 'DATASOURCE') {
								const valueDataSource: string[] = Object.entries(this.content[index]).find(el => el[0].toUpperCase() === field2.dataSource.toUpperCase());
								if (valueDataSource && formControl.value !== valueDataSource[1]) {
									const newValue: string = field2.mask ? this.utilsService.adjustMask(field2.mask, valueDataSource[1]) : valueDataSource[1];
									this.formArrayTable.controls[index].get(conditionOption.fieldName).setValue(newValue);
								}
							} else {
								this.formArrayTable.controls[index].get(conditionOption.fieldName).setValue(conditionOption.value);
							}
							
						};
					});
				};
			});
		};
	}

	private testFieldsToVisible(response: any, condition: Condition): void {

		if (response[condition.fieldName] == undefined) {
			return;
		}

		if (condition.value.toString() === response[condition.fieldName].toString()) {
			this.conditionsToVisible = [...this.conditionsToVisible, true]
		} else {
			this.conditionsToVisible = [...this.conditionsToVisible, false]
		}
	}

	private testFieldsToEnable(response: any, condition: Condition): void {

		if (response[condition.fieldName] == undefined) {
            return;
        }

		switch (condition.operator) {
			case 'equal':
				if (condition.value.toString() === response[condition.fieldName].toString()) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;

			case 'notEqual':
				if (condition.value.toString() !== response[condition.fieldName].toString()) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;
		
			case 'greaterThan':
				if (Number(condition.value) < Number(response[condition.fieldName])) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;
		
			case 'lessThan':
				if (Number(condition.value) > Number(response[condition.fieldName])) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;
		
			default:
				if (condition.value.toString() === response[condition.fieldName].toString()) {
					this.conditionsToEnable = [...this.conditionsToEnable, true]
				} else {
					this.conditionsToEnable = [...this.conditionsToEnable, false]
				}
				break;
		}
	}

	private enableField(field: Field, index: number): void {

		let result: boolean;

		switch (field.conditionsToEnableSatisfy) {
			case 'all':
				if (this.conditionsToEnable.indexOf(false) < 0) {
					result = true;
				} else {
					result = false;
				}
				break;
		
			case 'onlyOne':
				if (this.conditionsToEnable.indexOf(true) >= 0) {
					result = true;
				} else {
					result = false;
				}
				break;
		
			default:
				if (this.conditionsToEnable.indexOf(false) < 0) {
					result = true;
				} else {
					result = false;
				}
				break;
		}

		if (field.type === "button" || field.type === "upload") {
			if (result) {
				field.disabled = false;
			} else {
				field.disabled = true;
			}
		} else if (field.type === "input" && field.readOnly !== undefined && field.readOnly !== "") {
			if (result) {
				field.readOnly = false;
			} else {
				field.readOnly = true;
			}
		} else {
			if (result) {
				if (this.formArrayTable.controls[index].get(field.name).disabled) {
					this.formArrayTable.controls[index].get(field.name).enable();
				}
			} else {
				if (this.formArrayTable.controls[index].get(field.name).enabled) {
					this.formArrayTable.controls[index].get(field.name).disable();
				}
			}
		}
	}

	public showMessage(dialog: Dialog): void {
		const dialogRef = this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title : dialog.title,
				message : dialog.message,
				type : dialog.type,
			}
		});
		dialogRef.afterClosed().pipe(takeUntil(this.ngUnsubscribe)).subscribe();
    }
}
